import React, { useEffect, useState } from 'react';
import { createRoot } from 'react-dom/client';
import { QuickReplyForm } from './QuickReplyForm';
import { QuickReplyList } from './QuickReplyList';
import { isDarkMode } from '../../utils/utils';
import { postWindowMessage } from '../../utils/messageSender';
import { getActiveChatId } from '../../utils/WppFunctions';
import { icons } from '../../Constants/svg';

export const QuickReplyManager = ({ 
  isOpen = null, 
  isSidebarMode = true, 
  onClose = null 
}) => {
  const isDark = isDarkMode();

  const [showForm, setShowForm] = useState(false);
  const [showList, setShowList] = useState(true);
  const [editData, setEditData] = useState(null);
  const [editIndex, setEditIndex] = useState(null);
  const [currentChatId, setCurrentChatId] = useState(null);

  const [quickReplies, setQuickReplies] = useState(() => {
    try {
      return JSON.parse(localStorage.getItem('CQ_QUICK_REPLIES')) || [];
    } catch {
      return [];
    }
  });

  const [isLoading, setIsLoading] = useState(false);
  const [hasLoadedFromServer, setHasLoadedFromServer] = useState(false);

  // Listen for chat changes and update currentChatId
  useEffect(() => {
    // Get initial chat ID
    getActiveChatId().then(id => {
      setCurrentChatId(id);
    });

    // Listen for chat changes
    const handleChatChange = (chat) => {
      const chatId = chat?.id?._serialized;
      setCurrentChatId(chatId);
    };

    window.WPP.on('chat.active_chat', handleChatChange);

    return () => {
      window.WPP.off('chat.active_chat', handleChatChange);
    };
  }, []);

  // Load quick replies from server on first access
  useEffect(() => {
    const loadQuickReplies = async () => {
      if (hasLoadedFromServer || quickReplies.length > 0) return;
      
      setIsLoading(true);
      try {
        postWindowMessage('FETCH_QUICK_REPLIES', {});
        setHasLoadedFromServer(true);
      } catch (error) {
        console.error('Error loading quick replies:', error);
      } finally {
        setIsLoading(false);
      }
    };

    loadQuickReplies();
  }, [quickReplies.length, hasLoadedFromServer]);

  // Listen for quick replies from server
  useEffect(() => {
    const handleMessage = (event) => {
      if (event.source !== window) return;
      
      if (event.data.type === 'QUICK_REPLIES_LOADED') {
        const serverReplies = event.data.payload;
        if (serverReplies && Object.keys(serverReplies).length > 0) {
          // Convert server format to local format
          const convertedReplies = Object.entries(serverReplies).map(([replyName, replyDetail]) => ({
            name: replyName,
            message: replyDetail.message,
            editBeforeSend: replyDetail.editBeforeSend ?? false
          }));
          setQuickReplies(convertedReplies);
          saveToLocal(convertedReplies);
        }
      }
    };

    window.addEventListener('message', handleMessage);
    return () => window.removeEventListener('message', handleMessage);
  }, []);

  const saveToLocal = (replies) => {
    localStorage.setItem('CQ_QUICK_REPLIES', JSON.stringify(replies));
  };

  const saveToServer = async (replies) => {
    try {
      // Convert local format to server format
      const serverFormat = {};
      replies.forEach(reply => {
        if (reply.name && reply.message) {
          serverFormat[reply.name] = {message: reply.message, editBeforeSend: reply.editBeforeSend ?? false};
        }
      });

      const payload = Object.keys(serverFormat).length > 0 ? serverFormat : {};
      
      postWindowMessage('SAVE_QUICK_REPLIES', payload);
    } catch (error) {
      console.error('Error saving quick replies to server:', error);
    }
  };

  const handleReorder = (newList) => {
    setQuickReplies(newList);
    saveToLocal(newList);
    saveToServer(newList);
  };

  const handleSave = (data) => {
    const updated = [...quickReplies];
    if (editIndex !== null) {
      updated[editIndex] = data;
    } else {
      updated.push(data);
    }
    setQuickReplies(updated);
    saveToLocal(updated);
    saveToServer(updated);
    setEditData(null);
    setEditIndex(null);
    setShowForm(false);
    setShowList(true);
  };

  const handleDelete = (index) => {
    const updated = quickReplies.filter((_, i) => i !== index);
    setQuickReplies(updated);
    saveToLocal(updated);
    saveToServer(updated);
  };

  const handleEdit = (reply, index) => {
    setEditData(reply);
    setEditIndex(index);
    setShowForm(true);
    setShowList(false);
  };

  // Always show if isOpen is true in sidebar mode
  if (!isOpen) return null;

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%',
        overflow: 'hidden',
        backgroundColor: isDark ? '#1f2c33' : '#f0f2f5',
        padding: '0',
      }}
    >
      {currentChatId ? (
        <>
          {showForm ? (
            <div style={{ 
              padding: '20px',
              flex: 1,
              overflow: 'auto'
            }}>
              <QuickReplyForm
                initialData={editData || {}}
                onSave={handleSave}
                onCancel={() => {
                  setShowForm(false);
                  setShowList(true);
                  setEditData(null);
                  setEditIndex(null);
                }}
              />
            </div>
          ) : (
            <div style={{
              display: 'flex',
              flexDirection: 'column',
              height: '100%',
              overflow: 'hidden'
            }}>
              <div style={{
                flex: 1,
                overflow: 'auto',
                padding: '20px',
                paddingBottom: '100px'
              }}>
                <QuickReplyList
                  replies={quickReplies}
                  onDelete={handleDelete}
                  onEdit={handleEdit}
                  onAdd={() => {
                    setEditData(null);
                    setEditIndex(null);
                    setShowForm(true);
                    setShowList(false);
                  }}
                  onReorder={handleReorder}
                  isSidebarMode={true}
                />
              </div>
              <div style={{
                position: 'absolute',
                bottom: 0,
                left: 0,
                right: 0,
                padding: '20px',
                borderTop: `1px solid ${isDark ? '#2a3942' : '#e0e0e0'}`,
                backgroundColor: isDark ? '#111b21' : '#f0f2f5',
                zIndex: 10
              }}>
                <button
                  onClick={() => {
                    setEditData(null);
                    setEditIndex(null);
                    setShowForm(true);
                    setShowList(false);
                  }}
                  style={{
                    width: '100%',
                    backgroundColor: isDark ? '#00d9ff' : '#007bff',
                    color: isDark ? '#111b21' : '#fff',
                    border: 'none',
                    borderRadius: '8px',
                    padding: '12px 16px',
                    fontSize: '14px',
                    fontWeight: '600',
                    cursor: 'pointer',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    gap: '8px',
                    transition: 'all 0.2s ease',
                  }}
                  onMouseEnter={(e) => {
                    e.target.style.backgroundColor = isDark ? '#00c4e6' : '#0056b3';
                    e.target.style.transform = 'translateY(-1px)';
                    e.target.style.boxShadow = `0 4px 12px ${isDark ? 'rgba(0, 217, 255, 0.3)' : 'rgba(0, 123, 255, 0.3)'}`;
                  }}
                  onMouseLeave={(e) => {
                    e.target.style.backgroundColor = isDark ? '#00d9ff' : '#007bff';
                    e.target.style.transform = 'translateY(0)';
                    e.target.style.boxShadow = 'none';
                  }}
                >
                  <span style={{ fontSize: '16px', fontWeight: 'bold' }}>+</span>
                  Add Quick Reply
                </button>
              </div>
            </div>
          )}
        </>
      ) : (
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          padding: '40px 20px',
          textAlign: 'center',
          height: '100%'
        }}>
          <div style={{
            fontSize: '48px',
            color: isDark ? '#6b7280' : '#999',
            marginBottom: '20px',
            opacity: 0.5
          }}>
            {icons.QUICK_REPLY}
          </div>
          <div style={{
            fontSize: '18px',
            fontWeight: '600',
            color: isDark ? '#e9edef' : '#333',
            marginBottom: '8px'
          }}>
            Select Chat
          </div>
          <div style={{
            fontSize: '14px',
            color: isDark ? '#adb5bd' : '#666',
            lineHeight: '1.5',
            maxWidth: '280px'
          }}>
            Please select a chat to access quick replies.
          </div>
        </div>
      )}
    </div>
  );
};

