import React, { useState, useRef, useEffect } from 'react';

const MultiSelectDropdown = ({ options, selectedOptions, onChange, placeholder = "Select options..." }) => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef(null);

  useEffect(() => {
    // Inject CSS styles for multiselect dropdown
    const styleId = 'cq-multiselect-styles';
    let styleElement = document.getElementById(styleId);
    
    if (!styleElement) {
      styleElement = document.createElement('style');
      styleElement.id = styleId;
      document.head.appendChild(styleElement);
    }

    styleElement.textContent = `
      .cq-multiselect-box {
        padding: 8px;
        border: 1px solid #ccc;
        color: #111b21;
        border-radius: 6px;
        background: #fff;
        cursor: pointer;
        font-size: 14px;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 6px;
        box-sizing: border-box;
        transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
      }

      body.dark .cq-multiselect-box {
        border: 1px solid #313d45;
        background: #2a3942;
        color: #e9edef;
      }

      .cq-multiselect-tag {
        background: #e0f2ff;
        padding: 4px 8px;
        border-radius: 12px;
        display: flex;
        align-items: center;
        gap: 6px;
        font-size: 13px;
        color: #1f2937;
        transition: background-color 0.2s ease;
      }

      body.dark .cq-multiselect-tag {
        background: #00a884;
        color: #ffffff;
      }

      .cq-multiselect-tag-close {
        cursor: pointer;
        font-weight: bold;
        color: #555;
      }

      body.dark .cq-multiselect-tag-close {
        color: #ffffff;
      }

      .cq-multiselect-clear {
        margin-left: auto;
        color: #d32f2f;
        font-size: 12px;
        cursor: pointer;
        font-weight: bold;
        text-decoration: underline;
      }

      body.dark .cq-multiselect-clear {
        color: #ff6b6b;
      }

      .cq-multiselect-placeholder {
        color: #667781;
      }

      body.dark .cq-multiselect-placeholder {
        color: #8696a0;
      }

      .cq-multiselect-dropdown {
        position: absolute;
        width: 100%;
        top: 110%;
        left: 0;
        background: #fff;
        border: 1px solid #ccc;
        border-radius: 6px;
        max-height: 200px;
        overflow-y: auto;
        margin-top: 4px;
        z-index: 10;
        transition: background-color 0.2s ease, border-color 0.2s ease;
      }

      body.dark .cq-multiselect-dropdown {
        background: #2a3942;
        border: 1px solid #313d45;
      }

      .cq-multiselect-option {
        padding: 8px 10px;
        display: flex;
        align-items: center;
        cursor: pointer;
        font-size: 14px;
        color: #111b21;
        background: white;
        font-weight: normal;
        transition: background-color 0.2s ease, color 0.2s ease;
      }

      .cq-multiselect-option.selected {
        background: #f0faff;
        font-weight: bold;
      }

      body.dark .cq-multiselect-option {
        color: #e9edef;
        background: #2a3942;
      }

      body.dark .cq-multiselect-option.selected {
        background: #313d45;
      }

      .cq-multiselect-option:hover {
        background: #f5f5f5;
      }

      body.dark .cq-multiselect-option:hover {
        background: #3c4a54;
      }
    `;
  }, []);

  const toggleDropdown = () => setIsOpen(!isOpen);

  const handleOptionClick = (option) => {
    if (selectedOptions.includes(option)) {
      onChange(selectedOptions.filter((item) => item !== option));
    } else {
      onChange([...selectedOptions, option]);
    }
  };

  const handleRemoveOption = (option) => {
    onChange(selectedOptions.filter((item) => item !== option));
  };

  const handleClearAll = (e) => {
    e.stopPropagation(); // prevent opening dropdown
    onChange([]);
  };

  const handleClickOutside = (event) => {
    if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
      setIsOpen(false);
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  return (
    <div ref={dropdownRef} style={{ position: 'relative', width: '100%' }}>
      {/* Display box */}
      <div
        onClick={toggleDropdown}
        className="cq-multiselect-box"
      >
        {selectedOptions.length > 0 ? (
          <>
            {selectedOptions.map((option) => (
              <div
                key={option}
                className="cq-multiselect-tag"
                onClick={(e) => e.stopPropagation()} // prevent reopening dropdown
              >
                {option}
                <span
                  onClick={() => handleRemoveOption(option)}
                  className="cq-multiselect-tag-close"
                >
                  
                </span>
              </div>
            ))}
            <div
              onClick={handleClearAll}
              className="cq-multiselect-clear"
            >
              Clear All
            </div>
          </>
        ) : (
          <span className="cq-multiselect-placeholder">{placeholder}</span>
        )}
      </div>

      {/* Dropdown menu */}
      {isOpen && (
        <div className="cq-multiselect-dropdown">
          {options.map((option) => (
            <div
              key={option}
              onClick={() => handleOptionClick(option)}
              className={`cq-multiselect-option ${selectedOptions.includes(option) ? 'selected' : ''}`}
            >
              <input
                type="checkbox"
                checked={selectedOptions.includes(option)}
                readOnly
                style={{ marginRight: '8px' }}
              />
              {option}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default MultiSelectDropdown;
