import React, { useState, useEffect } from 'react';

const RelativeTime = ({ timestamp, isDark = false }) => {
  const [relativeTime, setRelativeTime] = useState('');

  const getRelativeTime = (timestamp) => {
    const now = new Date();
    const time = new Date(timestamp);
    const diffInSeconds = Math.floor((now - time) / 1000);
    const diffInMinutes = Math.floor(diffInSeconds / 60);
    const diffInHours = Math.floor(diffInMinutes / 60);
    const diffInDays = Math.floor(diffInHours / 24);

    if (diffInSeconds < 60) {
      return 'just now';
    } else if (diffInMinutes < 60) {
      return `${diffInMinutes}m ago`;
    } else if (diffInHours < 24) {
      return `${diffInHours}h ago`;
    } else if (diffInDays < 7) {
      return `${diffInDays}d ago`;
    } else {
      return time.toLocaleDateString();
    }
  };

  const updateRelativeTime = () => {
    setRelativeTime(getRelativeTime(timestamp));
  };

  useEffect(() => {
    updateRelativeTime();
    
    const interval = setInterval(updateRelativeTime, 60000);
    
    return () => clearInterval(interval);
  }, [timestamp]);

  const getDetailedTime = (timestamp) => {
    const time = new Date(timestamp);
    const dayOptions = { weekday: 'short' };
    const monthOptions = { month: 'long' };
    const timeOptions = { hour: 'numeric', minute: '2-digit', hour12: true };
    
    const dayName = time.toLocaleDateString('en-US', dayOptions);
    const monthName = time.toLocaleDateString('en-US', monthOptions);
    const day = time.getDate();
    const year = time.getFullYear();
    const timeString = time.toLocaleTimeString('en-US', timeOptions);
    
    return `${dayName} ${monthName} ${day}, ${year} ${timeString}`;
  };

  return (
    <span 
      title={getDetailedTime(timestamp)}
      style={{
        fontSize: '11px',
        color: isDark ? '#8696a0' : '#667781',
        opacity: 0.8,
        fontWeight: '400'
      }}
    >
      {relativeTime}
    </span>
  );
};

export default RelativeTime;