import React, { useState } from 'react';
import ReactDOM from 'react-dom/client';
import OnBoardingForm from '../Components/OnBoardingForm/OnBoardingForm';
import WelcomePage from '../Components/WelcomePage/WelcomePage';
import { TRACK_EVENTS } from '../Constants/constant';
import { postWindowMessage } from './messageSender';


export const onBoarding = (OnboardingData = null, isUpdate = false, showWelcomePage = false) => {
    // if (localStorage.getItem('IS_USER_ONBOARDED') === 'true') {
    //     return; // already onboarded
    // }
    const OVERLAY_ID = 'cq-onboarding';
    const BODY_CLASS = 'cq-body-class';
    // Add class to <body>
    document.body.classList.add(BODY_CLASS);

    // Don't inject again if already exists
    if (document.getElementById(OVERLAY_ID)) return;

    // Create and style overlay div
    const overlay = document.createElement('div');
    overlay.id = OVERLAY_ID;

    // Fullscreen overlay styling
    overlay.style.position = 'fixed';
    overlay.style.top = '0';
    overlay.style.left = '0';
    overlay.style.right = '0';
    overlay.style.bottom = '0';
    overlay.style.backgroundColor = 'rgba(0, 0, 0, 0.5)';
    overlay.style.zIndex = '9999';
    overlay.style.display = 'flex';
    overlay.style.alignItems = 'flex-start';
    overlay.style.justifyContent = 'center';
    overlay.style.overflow = 'auto';
    overlay.style.padding = '30px';

    document.body.appendChild(overlay);

    // Mount your form component
    const root = ReactDOM.createRoot(overlay);
    // root.render(<OnBoardingForm OnboardingData={OnboardingData} isUpdate={isUpdate}/>);
    if (showWelcomePage) {
        postWindowMessage('TRACK_EVENT', {eventName: TRACK_EVENTS.WELCOME_PAGE_OPENED});
    } else if (isUpdate) {
        postWindowMessage('TRACK_EVENT', {eventName: TRACK_EVENTS.UPDATE_PROFILE_FORM_OPENED});
    } else {
        postWindowMessage('TRACK_EVENT', {eventName: TRACK_EVENTS.SIGN_UP_FROM_OPENED});
    }

    
    const RenderComponent = () => {
        const [showWelcome, setShowWelcome] = useState(showWelcomePage);
        const onGetStarted = () => {
            postWindowMessage('TRACK_EVENT', {eventName: TRACK_EVENTS.WELCOME_PAGE_GET_STARTED_CLICKED});
            setShowWelcome(false);
        };

        return showWelcome ? (
            <WelcomePage onGetStarted={onGetStarted} />
        ) : (
            <OnBoardingForm OnboardingData={OnboardingData} isUpdate={isUpdate} />
        );
    };
    root.render(<RenderComponent />);
}
