import React from 'react';
import { createPortal } from 'react-dom';
import { icons } from '../../Constants/svg';

const ConfirmationModal = ({ 
  isOpen, 
  onClose, 
  onConfirm, 
  title = "Confirmation", 
  message = "Are you sure you want to proceed?",
  confirmText = "Confirm",
  cancelText = "Cancel",
  confirmButtonStyle = "primary"
}) => {
  if (!isOpen) return null;

  const handleOverlayClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  const confirmButtonClass = confirmButtonStyle === "danger" 
    ? "cq-confirmation-confirm-danger" 
    : "cq-confirmation-confirm-primary";

  return createPortal(
    <div className="cq-confirmation-modal-overlay" onClick={handleOverlayClick}>
      <div className="cq-confirmation-modal">
        <div className="cq-confirmation-modal-header">
          <h3 className="cq-confirmation-modal-title">{title}</h3>
          <button 
            className="cq-confirmation-modal-close" 
            onClick={onClose}
            aria-label="Close"
          >
            {icons.CROSS_CIRCLED}
          </button>
        </div>
        
        <div className="cq-confirmation-modal-body">
          <p className="cq-confirmation-modal-message">{message}</p>
        </div>
        
        <div className="cq-confirmation-modal-footer">
          <button 
            className="cq-confirmation-cancel"
            onClick={onClose}
          >
            {cancelText}
          </button>
          <button 
            className={confirmButtonClass}
            onClick={() => {
              onConfirm();
              onClose();
            }}
          >
            {confirmText}
          </button>
        </div>
      </div>
    </div>,
    document.body
  );
};

export default ConfirmationModal;