import React, { useState, useRef, useEffect } from 'react';
import { Button } from '@radix-ui/themes';
import InputBox from '../UIComponents/InputBox';
import TextAreaBox from '../UIComponents/TextAreaBox';
import Checkbox from '../UIComponents/Checkbox';

export const QuickReplyForm = ({ onSave, onCancel, initialData = {} }) => {
  const [formData, setFormData] = useState({
    name: '',
    message: '',
    editBeforeSend: false,
  });

  const [error, setError] = useState('');
  const formRef = useRef(null);

  useEffect(() => {
    formRef.current?.focus();
  }, []);

  // Update form data when initialData changes
  useEffect(() => {
    setFormData({
      name: initialData?.name || '',
      message: initialData?.message || '',
      editBeforeSend: initialData?.editBeforeSend || false,
    });
  }, [initialData]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleCheckboxChange = (e) => {
    const isChecked = e.target.checked;
    setFormData((prev) => ({ ...prev, editBeforeSend: isChecked }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!formData.name.trim()) {
      setError('Name is required');
      return;
    }
    if (!formData.message.trim()) {
      setError('Message is required');
      return;
    }
    setError('');
    onSave(formData);
  };

  const isDark = document.body.classList.contains('dark');

  return (
    <div
        style={{
          backgroundColor: 'transparent',
          color: isDark ? '#e9edef' : '#333',
          padding: '0',
          borderRadius: '0',
          boxShadow: 'none',
          minWidth: 'auto',
          maxWidth: 'none',
          width: '100%',
          zIndex: 'auto',
          position: 'relative',
          transform: 'none',
          top: 'auto',
          left: 'auto'
        }}
      >
      <h2 style={{ 
        marginBottom: '1.5rem', 
        fontSize: '1.125rem',
        color: isDark ? '#e9edef' : '#333'
      }}>
        {initialData?.name ? 'Edit Quick Reply' : 'Create Quick Reply'}
      </h2>

      <form ref={formRef} onSubmit={handleSubmit}>
        {/* Name Input */}
        <div style={{ marginBottom: '1.5rem' }}>
          <label style={{ 
            fontSize: '0.9rem', 
            marginBottom: '0.5rem', 
            display: 'block',
            color: isDark ? '#e9edef' : '#333',
            fontWeight: '500'
          }}>
            Name
          </label>
          <InputBox
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="Welcome"
          />
        </div>

        {/* Message Input */}
        <div style={{ marginBottom: '1.5rem' }}>
          <label style={{ 
            fontSize: '0.9rem', 
            marginBottom: '0.5rem', 
            display: 'block',
            color: isDark ? '#e9edef' : '#333',
            fontWeight: '500'
          }}>
            Message
          </label>
          <TextAreaBox
            name="message"
            value={formData.message}
            onChange={handleChange}
            placeholder="Hi, how are you?"
          />
        </div>

        {/* Checkbox */}
        <div style={{ marginBottom: '1.5rem' }}>
          <Checkbox
            id="edit-before-send"
            label="Edit before send"
            checked={formData.editBeforeSend}
            onChange={handleCheckboxChange}
            labelColor={isDark ? '#e9edef' : '#1f2937'}
          />
        </div>

        {/* Error */}
        {error && <p style={{ 
          color: '#dc3545', 
          marginBottom: '1.5rem',
          fontSize: '0.875rem'
        }}>{error}</p>}

        {/* Actions */}
        <div style={{ 
          display: 'flex', 
          justifyContent: 'flex-end', 
          gap: '0.75rem',
          marginTop: '2rem'
        }}>
          <Button
            variant="solid"
            color="ghost"
            size="3"
            style={{ 
              padding: '8px 16px', 
              borderRadius: '6px', 
              cursor: 'pointer', 
              color: isDark ? '#e9edef' : '#1f2937',
              backgroundColor: isDark ? 'transparent' : 'transparent',
              border: `1px solid ${isDark ? '#3b4a54' : '#e0e0e0'}`
            }}
            onClick={onCancel}
          >
            Cancel
          </Button>
          <Button
            variant="solid"
            color="blue"
            size="3"
            type="submit"
            style={{ 
              padding: '8px 16px', 
              borderRadius: '6px', 
              cursor: 'pointer',
              backgroundColor: isDark ? '#00d9ff' : '#007bff',
              color: isDark ? '#111b21' : '#fff',
              border: 'none'
            }}
          >
            Save
          </Button>
        </div>
      </form>
    </div>
  );
};
