import React, { useState, useEffect, useRef } from 'react';

import { Button } from '@radix-ui/themes';
import Checkbox from '../UIComponents/Checkbox';
import TextAreaBox from '../UIComponents/TextAreaBox';
import { showSuggestionPreview } from '../../utils/showSuggestionPreview';
import { getActiveChatMessages } from '../../utils/WppFunctions';
import { postWindowMessage } from '../../utils/messageSender';
import { stripHtml, getStaticMessage, getTrialDaysLeft } from '../../utils/utils';
import { TONE_OPTIONS, MESSAGE_LENGTH_OPTIONS, TRACK_EVENTS } from '../../Constants/constant';


const RephraseForm = ({ onCancel, aiPrompt = '', label = 'Rephrase' }) => {
  const [formData, setFormData] = useState({
    message_length: '',
    tone: [],
    custom_instruction: '',
  });

  const formRef = useRef(null);
  const [error, setError] = useState('');

  useEffect(() => {
    formRef.current?.focus();
    const savedData = localStorage.getItem('rephraseFormData');
    if (savedData) {
      setFormData(JSON.parse(savedData));
    }
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleToneToggle = (tone) => {
    setFormData((prev) => {
      const current = prev.tone || [];
      const updated = current.includes(tone)
        ? current.filter((t) => t !== tone)
        : [...current, tone];
      return { ...prev, tone: updated };
    });
  };

  const handleMessageLengthToggle = (message_length) => {
    setFormData((prev) => ({
      ...prev,
      message_length: prev.message_length === message_length ? '' : message_length,
    }));
  };

  const handleKeyDown = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleSubmit(e);
    }
  };

  const showTrialMessage = () => {
    const trialExpiry = localStorage.getItem('CQ_USER_TRIAL_EXPIRY');
    if (!trialExpiry) return '';

    let trialMessage = '';

    const daysLeft = getTrialDaysLeft(trialExpiry);
    if (daysLeft > 0) {
      trialMessage = getStaticMessage('MESSAGE_TRIAL_DAYS_COUNT').replace('#', daysLeft.toString());
    } else {
      trialMessage = getStaticMessage('MESSAGE_TRIAL_EXPIRED') || 'Your trial has expired.';
    }
    return stripHtml(trialMessage);
  }

  const handleSubmit = async (e) => {
    e.preventDefault();

    showSuggestionPreview();
    const chatMessages = await getActiveChatMessages();

    const advanceInstruction = {};

    if (formData.custom_instruction) {
      advanceInstruction.custom_instruction = formData.custom_instruction;
    }
    if (formData.tone && formData.tone.length > 0) {
      advanceInstruction.tone = formData.tone;
    }
    if (formData.message_length) {
      advanceInstruction.message_length = formData.message_length;
    }

    let eventName = '';
    let getReply = 'GET_AI_REPLY';

    if (label === 'Rephrase') {
      advanceInstruction.message = aiPrompt;
      advanceInstruction.is_customer = false;
      advanceInstruction.name = 'you';
      advanceInstruction.timestamp = Date.now();
      chatMessages.rephrase_suggestion = advanceInstruction;

      eventName = TRACK_EVENTS.ADVANCE_REPHRASE_FORM_SUBMITTED;
      getReply = 'GET_ADVANCE_REPHRASE_REPLY';
    } else if (label === 'Generate' && Object.keys(advanceInstruction).length > 0) {
      chatMessages.extra_instruction = advanceInstruction;

      eventName = TRACK_EVENTS.ADVANCE_GENERATE_FORM_SUBMITTED;
      getReply = 'GET_ADVANCE_GENERATE_REPLY';
    }


    postWindowMessage(getReply, chatMessages);
    postWindowMessage('TRACK_EVENT', { eventName: eventName });
    window.dispatchEvent(new CustomEvent('SET_AIBUTTON_LOADING_TRUE'));

    formData.message_length = '';
    formData.custom_instruction = '';
    localStorage.setItem('rephraseFormData', JSON.stringify(formData));
    if (onCancel) onCancel(false, true);
  };

  return (
    <div
      style={{
        backgroundColor: '#fff',
        padding: '16px',
        borderRadius: '12px',
        fontFamily: 'sans-serif',
      }}
    >
      <h2 style={{ marginBottom: '1.5rem', fontSize: '1.5rem', color: '#1f2937' }}>
        {label} Message
      </h2>
      {showTrialMessage() && (
        <p
          style={{
            backgroundColor: '#FEF3C7', // soft yellow background
            color: '#92400E',           // dark amber text
            fontSize: '0.875rem',
            padding: '0.5rem 0.75rem',
            borderRadius: '6px',
            margin: '0.5rem 0 1.25rem 0',
            border: '1px solid #FCD34D',
          }}
        >
          {showTrialMessage()}
        </p>
      )}

      <form ref={formRef} tabIndex={-1} onSubmit={handleSubmit} onKeyDown={handleKeyDown}>
        <button type="submit" style={{ display: 'none' }} aria-hidden="true" />
        {/* Tone */}
        <div style={{ marginBottom: '1rem' }}>
          <label style={{ fontSize: '0.875rem', color: '#374151', marginBottom: '0.5rem', display: 'block' }}>
            Tone
          </label>
          <div
            style={{
              border: '1px solid #ccc',
              borderRadius: '6px',
              padding: '10px',
              display: 'grid',
              gridTemplateColumns: 'repeat(2, 1fr)',
              gap: '10px',
              maxHeight: '100px',
              overflowY: 'auto',
            }}
          >
            {TONE_OPTIONS.map((tone) => (
              <Checkbox
                key={tone}
                id={`tone-${tone}`}
                label={tone}
                checked={formData.tone.includes(tone)}
                onChange={() => handleToneToggle(tone)}
              />
            ))}
          </div>
        </div>

        {/* Message Length */}
        <div style={{ marginBottom: '1rem' }}>
          <label style={{ fontSize: '0.875rem', color: '#374151', marginBottom: '0.5rem', display: 'block' }}>
            Change Message Length
          </label>
          <div
            style={{
              border: '1px solid #ccc',
              borderRadius: '6px',
              padding: '10px',
              display: 'grid',
              gridTemplateColumns: 'repeat(3, 1fr)',
              gap: '10px',
            }}
          >
            {MESSAGE_LENGTH_OPTIONS.map((message_length) => (
              <Checkbox
                key={message_length}
                id={`message_length-${message_length}`}
                label={message_length}
                checked={formData.message_length === message_length}
                onChange={() => handleMessageLengthToggle(message_length)}
              />
            ))}
          </div>
        </div>

        {/* Custom Instruction */}
        <div style={{ marginBottom: '1rem' }}>
          <label htmlFor="custom_instruction" style={{ fontSize: '0.875rem', color: '#374151', marginBottom: '0.25rem', display: 'block' }}>
            Custom Instruction
          </label>
          <TextAreaBox
            name="custom_instruction"
            value={formData.custom_instruction}
            onChange={handleChange}
            placeholder="Enter your instruction"
          />
        </div>

        {/* Error */}
        {error && (
          <p style={{ color: 'red', fontSize: '0.875rem', marginBottom: '1rem' }}>
            {error}
          </p>
        )}

        {/* Buttons */}
        <div style={{ display: 'flex', justifyContent: 'flex-end', gap: '0.5rem', marginTop: '1rem' }}>
          <Button
            variant="solid"
            color="ghost"
            size="3"
            style={{ padding: '5px', borderRadius: '5px', cursor: 'pointer', color: '#1f2937' }}
            onClick={(e) => {
              e.preventDefault();
              if (onCancel) onCancel();
            }}
          >
            Cancel
          </Button>
          <Button
            variant="solid"
            color="blue"
            size="3"
            type="submit"
            style={{ padding: '5px', borderRadius: '5px', cursor: 'pointer' }}
          >
            {label}
          </Button>
        </div>
      </form>
    </div>
  );
};

export default RephraseForm;
