/* HTML Content Styling for Markdown-converted elements */
.cq-html-content {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
}

/* Headings */
.cq-html-content h1 {
  font-size: 24px;
  font-weight: 700;
  color: #1a1a1a;
  margin: 24px 0 16px 0;
  padding-bottom: 8px;
  border-bottom: 2px solid #e1e4e8;
  line-height: 1.25;
}

.cq-html-content h2 {
  font-size: 20px;
  font-weight: 600;
  color: #1a1a1a;
  margin: 20px 0 12px 0;
  padding-bottom: 6px;
  border-bottom: 1px solid #e1e4e8;
  line-height: 1.3;
}

.cq-html-content h3 {
  font-size: 18px;
  font-weight: 600;
  color: #1a1a1a;
  margin: 16px 0 10px 0;
  line-height: 1.3;
}

.cq-html-content h4 {
  font-size: 16px;
  font-weight: 600;
  color: #1a1a1a;
  margin: 14px 0 8px 0;
  line-height: 1.3;
}

.cq-html-content h5 {
  font-size: 14px;
