  color: #1a1a1a;
  margin: 12px 0 6px 0;
  line-height: 1.3;
}

.cq-html-content h6 {
  font-size: 13px;
  font-weight: 600;
  color: #6a737d;
  margin: 10px 0 6px 0;
  line-height: 1.3;
}

/* Dark mode headings */
