}

.cq-html-content ul ul ul {
  list-style-type: square;
}

.cq-html-content ol {
  list-style-type: decimal;
}

.cq-html-content ol ol {
  list-style-type: lower-alpha;
  margin: 4px 0;
}

.cq-html-content ol ol ol {
  list-style-type: lower-roman;
}

/* Nested lists */
.cq-html-content li > ul,
.cq-html-content li > ol {
  margin-top: 4px;
  margin-bottom: 4px;
}

/* Blockquotes */
.cq-html-content blockquote {
  margin: 16px 0;
  padding: 0 16px;
  border-left: 4px solid #dfe2e5;
  color: #6a737d;
  font-style: italic;
  background-color: #f8f9fa;
  border-radius: 0 4px 4px 0;
