.cq-html-content code {
  background-color: #f3f4f6;
  color: #d73a49;
  padding: 2px 4px;
  border-radius: 3px;
  font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, monospace;
  font-size: 13px;
  white-space: nowrap;
