  border-bottom-color: #586069;
  box-shadow: inset 0 -1px 0 #586069;
  color: #e9edef;
}

/* Mark (highlighting) */
.cq-html-content mark {
  background-color: #fff3cd;
  padding: 2px 4px;
  border-radius: 2px;
  color: inherit;
}

body.dark .cq-html-content mark {
  background-color: #3d3014;
}

/* Small text */
.cq-html-content small {
  font-size: 12px;
