750;\n}\n\nbody.dark .cq-summarizer-header.outdated {\n  background-color: #3d3014;\n  border-bottom-color: #ffc107;\n}\n\n.cq-summarizer-status {\n  display: flex;\n  align-items: center;\n  gap: 8px;\n  flex: 1;\n}\n\n.cq-summarizer-status-icon {\n  font-size: 14px;\n}\n\n.cq-summarizer-status-text {\n  font-size: 12px;\n  color: #666;\n}\n\nbody.dark .cq-summarizer-status-text {\n  color: #adb5bd;\n}\n\n.cq-summarizer-actions {\n  display: flex;\n  gap: 8px;\n}\n\n.cq-summarizer-action-btn {\n  background: none;\n  border: 1px solid #ddd;\n  border-radius: 6px;\n  width: 32px;\n  height: 32px;\n  cursor: pointer;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  transition: all 0.2s ease;\n  color: #666;\n}\n\n.cq-summarizer-action-btn:hover {\n  background-color: #f0f0f0;\n  border-color: #999;\n}\n\n.cq-summarizer-action-btn:disabled {\n  opacity: 0.6;\n  cursor: not-allowed;\n}\n\nbody.dark .cq-summarizer-action-btn {\n  border-color: #555;\n  color: #adb5bd;\n}\n\nbody.dark .cq-summarizer-action-btn:hover {\n  background-color: #374151;\n  border-color: #6b7280;\n}\n\n.cq-summarizer-content {\n  flex: 1;\n  overflow-y: auto;\n  padding: 20px;\n}\n\n.cq-summarizer-loading {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  justify-content: center;\n  height: 200px;\n  gap: 16px;\n  color: #666;\n}\n\nbody.dark .cq-summarizer-loading {\n  color: #adb5bd;\n}\n\n.cq-loading-spinner {\n  width: 32px;\n  height: 32px;\n  border: 3px solid #e0e0e0;\n  border-top: 3px solid #007bff;\n  border-radius: 50%;\n  animation: spin 1s linear infinite;\n}\n\nbody.dark .cq-loading-spinner {\n  border-color: #555;\n  border-top-color: #60a5fa;\n}\n\n@keyframes spin {\n  0% { transform: rotate(0deg); }\n  100% { transform: rotate(360deg); }\n}\n\n.cq-spinner {\n  animation: spin 1s linear infinite;\n  display: inline-block;\n}\n\n.cq-summarizer-text {\n  background-color: #f8f9fa;\n  padding: 16px;\n  border-radius: 8px;\n  border: 1px solid #e0e0e0;\n  line-height: 1.6;\n  font-size: 14px;\n  color: #333;\n}\n\nbody.dark .cq-summarizer-text {\n  background-color: #2a3942;\n  border-color: #3a4750;\n  color: #e9edef;\n}\n\n/* HTML Content Styling for Markdown-converted elements */\n.cq-html-content {\n  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;\n}\n\n/* Headings */\n.cq-html-content h1 {\n  font-size: 24px;\n  font-weight: 700;\n  color: #1a1a1a;\n  margin: 24px 0 16px 0;\n  padding-bottom: 8px;\n  border-bottom: 2px solid #e1e4e8;\n  line-height: 1.25;\n}\n\n.cq-html-content h2 {\n  font-size: 20px;\n  font-weight: 600;\n  color: #1a1a1a;\n  margin: 20px 0 12px 0;\n  padding-bottom: 6px;\n  border-bottom: 1px solid #e1e4e8;\n  line-height: 1.3;\n}\n\n.cq-html-content h3 {\n  font-size: 18px;\n  font-weight: 600;\n  color: #1a1a1a;\n  margin: 16px 0 10px 0;\n  line-height: 1.3;\n}\n\n.cq-html-content h4 {\n  font-size: 16px;\n  font-weight: 600;\n  color: #1a1a1a;\n  margin: 14px 0 8px 0;\n  line-height: 1.3;\n}\n\n.cq-html-content h5 {\n  font-size: 14px;\n  font-weight: 600;\n  color: #1a1a1a;\n  margin: 12px 0 6px 0;\n  line-height: 1.3;\n}\n\n.cq-html-content h6 {\n  font-size: 13px;\n  font-weight: 600;\n  color: #6a737d;\n  margin: 10px 0 6px 0;\n  line-height: 1.3;\n}\n\n/* Dark mode headings */\nbody.dark .cq-html-content h1,\nbody.dark .cq-html-content h2,\nbody.dark .cq-html-content h3,\nbody.dark .cq-html-content h4,\nbody.dark .cq-html-content h5 {\n  color: #e9edef;\n}\n\nbody.dark .cq-html-content h6 {\n  color: #adb5bd;\n}\n\nbody.dark .cq-html-content h1 {\n  border-bottom-color: #3a4750;\n}\n\nbody.dark .cq-html-content h2 {\n  border-bottom-color: #3a4750;\n}\n\n/* Paragraphs */\n.cq-html-content p {\n  margin: 12px 0;\n  line-height: 1.6;\n  color: inherit;\n}\n\n.cq-html-content p:first-child {\n  margin-top: 0;\n}\n\n.cq-html-content p:last-child {\n  margin-bottom: 0;\n}\n\n/* Lists */\n.cq-html-content ul,\n.cq-html-c