import { BaseRepository } from './BaseRepository.js';

/** DB Config */
const CONTACT_DB_NAME = 'model-storage';
const CONTACT_STORE_NAME = 'contact';

/** Contact Repository */
export class ContactRepository extends BaseRepository {
  constructor() {
    super(CONTACT_DB_NAME, CONTACT_STORE_NAME);
  }

  /**
   * Normalize incoming contactId to all possible internal formats
   * @param {string} contactId Original contact ID (e.g. 91999999999@c.us)
   * @returns {string[]} Possible DB keys to check
   */
  static getNormalizedIds(contactId) {
    const baseId = contactId.split('@')[0];
    return [
      `${baseId}@s.whatsapp.net`,
      `${baseId}@lid`,
    ];
  }

  /**
   * Get contact name for a given WhatsApp ID, normalized internally.
   * @param {string} contactId ID in @c.us or @g.us format
   * @returns {Promise<string>} Contact name or "No record found"
   */
  async getContactName(contactId) {
    return this.safeExecute(async () => {
      if (!contactId) throw new Error('Contact ID is required');

      const possibleIds = ContactRepository.getNormalizedIds(contactId);

      for (const id of possibleIds) {
        const record = await this.getByKey(id);
        console.log('Record found for ID', id, record);
        if (record) {
          return record.name || record.pushname || record.shortName || '';
        }
      }

      return 'No record found';
    }, 'getContactName');
  }

  async isAddressBookContact(contactId) {
    return this.safeExecute(async () => {
      if (!contactId) throw new Error('Contact ID is required');

      const possibleIds = ContactRepository.getNormalizedIds(contactId);

      for (const id of possibleIds) {
        const record = await this.getByKey(id);
        console.log('Record found for ID', id, record);
        if (record) {
          return !!record.isAddressBookContact;
        }
      }

      return false;
    }, 'isAddressBookContact'); 
  } 
}

export const contactRepository = new ContactRepository();
