import React, { useState, useRef, useEffect } from 'react';
import { icons } from '../../Constants/svg';
import Icon from '../UIComponents/Icon';
import { renderSidePanel } from '../../utils/renderSidePanel';
import ChatNotes from '../ChatNotes/ChatNotes';
import ChatTodo from '../ChatTodo/ChatTodo';
import Reminders from '../Reminders/Reminders';
import { postWindowMessage } from '../../utils/messageSender';
import { TRACK_EVENTS } from '../../Constants/constant';
import './TasksFolder.css';

const TasksFolder = ({ noteCount, todoCount, reminderCount, currentChatId, checkOnboardingAndExecute }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const menuRef = useRef(null);
  const folderRef = useRef(null);

  // Close menu when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        menuRef.current &&
        !menuRef.current.contains(event.target) &&
        folderRef.current &&
        !folderRef.current.contains(event.target)
      ) {
        setIsMenuOpen(false);
      }
    };

    if (isMenuOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    }

    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isMenuOpen]);

  const totalCount = noteCount + todoCount + reminderCount;

  const handleFolderClick = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleMenuItemClick = (component, props, panelConfig, eventName) => {
    checkOnboardingAndExecute(() => {
      postWindowMessage('TRACK_EVENT', { eventName });
      renderSidePanel(component, props, panelConfig);
      setIsMenuOpen(false);
    });
  };

  const menuItems = [
    {
      icon: icons.NOTE,
      label: 'Notes',
      count: noteCount,
      onClick: () =>
        handleMenuItemClick(
          ChatNotes,
          { isOpen: true },
          {
            title: 'Chat Notes',
            containerId: 'chat-notes-panel'
          },
          TRACK_EVENTS.NOTES_PANEL_OPENED
        ),
    },
    {
      icon: icons.REMINDER,
      label: 'Reminders',
      count: reminderCount,
      onClick: () =>
        handleMenuItemClick(
          Reminders,
          { isOpen: true },
          {
            title: 'Reminders',
            containerId: 'reminders-panel'
          },
          TRACK_EVENTS.REMINDERS_PANEL_OPENED
        ),
    },
    {
      icon: icons.TODO,
      label: 'Todo',
      count: todoCount,
      onClick: () =>
        handleMenuItemClick(
          ChatTodo,
          { isOpen: true },
          {
            title: 'Chat Todo',
            containerId: 'chat-todo-panel'
          },
          TRACK_EVENTS.TODO_PANEL_OPENED
        ),
    },
  ];

  return (
    <div className="cq-tasks-folder-container">
      <div ref={folderRef}>
        <Icon
          tooltip="Todo & Reminders"
          icon={icons.FOLDER}
          label="Todo & Reminders"
          onClick={handleFolderClick}
          badge={currentChatId ? totalCount : 0}
          badgeType="info"
        />
      </div>

      {isMenuOpen && (
        <div ref={menuRef} className="cq-tasks-folder-menu">
          {menuItems.map((item, index) => (
            <div
              key={index}
              className="cq-tasks-menu-item"
              onClick={item.onClick}
            >
              <div className="cq-tasks-menu-icon">
                {item.icon}
                {currentChatId && item.count > 0 && (
                  <div className="cq-tasks-menu-badge">{item.count}</div>
                )}
              </div>
              <div className="cq-tasks-menu-label">{item.label}</div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default TasksFolder;
