import React from 'react';
import ReactDOM from 'react-dom/client';
import AiButton from '../Components/AiButton/AiButton';

export const injectAiButton = () => {
  const footer = document.querySelector('footer._ak1i');
  if (!footer && document.querySelector('.cq-wrapper')) return;

  const inputWrapper = footer.querySelector('.lexical-rich-text-input');
  if (!inputWrapper || !inputWrapper.parentElement) return;

  const parent = inputWrapper.parentElement;

  // Remove existing button if re-injecting
  const cqWrapper = document.querySelector('.cq-wrapper');
  if (cqWrapper) return;

  // Create button container
  const wrapper = document.createElement('div');
  wrapper.className = 'cq-wrapper';

  Object.assign(wrapper.style, {
    alignSelf: 'flex-end',
    display: 'flex',
    boxSizing: 'border-box',
    alignItems: 'center',
    justifyContent: 'center',
    height: '40px',
    marginLeft: '10px',
    marginRight: '10px',
  });

  // Insert the wrapper right after inputWrapper
  if (inputWrapper.nextSibling) {
    parent.insertBefore(wrapper, inputWrapper.nextSibling);
  } else {
    parent.appendChild(wrapper); // Fallback in case there's no sibling
  }

  const root = ReactDOM.createRoot(wrapper);
  root.render(<AiButton />);

};
