import React, { useState, useEffect, useRef } from 'react';

const Popover = ({
    message = 'Popover message here',
    targetSelector = '',
    arrowPosition = 'left',
    onClose = null,
    popoverId = 'default',
}) => {
    const [visible, setVisible] = useState(false);
    const [coords, setCoords] = useState({ top: 200, left: 350 });
    const popoverRef = useRef(null);
    const width = 280;
    const offset = 10;

    const calculatePosition = () => {
        const target = document.querySelector(targetSelector);
        const popover = popoverRef.current;

        if (!target || !popover) {
            if (!target) {
                console.warn(`Popover target not found: ${targetSelector}`);
            }
            return;
        }

        const rect = target.getBoundingClientRect();
        const scrollY = window.scrollY;
        const scrollX = window.scrollX;
        const popoverHeight = popover.offsetHeight || 70;

        const placements = {
            top: {
                top: rect.top + scrollY - popoverHeight - offset,
                left: rect.left + scrollX + rect.width / 2 - width / 2,
            },
            topRight: {
                top: rect.top + scrollY - popoverHeight - offset,
                left: rect.right + scrollX - width,
            },
            bottom: {
                top: rect.bottom + scrollY + offset,
                left: rect.left + scrollX + rect.width / 2 - width / 2,
            },
            bottomLeft: {
                top: rect.bottom + scrollY + offset,
                left: rect.left + scrollX,
            },
            left: {
                top: rect.top + scrollY + rect.height / 2 - popoverHeight / 2,
                left: rect.left + scrollX - width - offset,
            },
            leftBottom: {
                top: rect.bottom + scrollY - popoverHeight + 10,
                left: rect.left + scrollX - width - offset - 20,
            },
            right: {
                top: rect.top + scrollY + rect.height / 2 - popoverHeight / 2,
                left: rect.right + scrollX + offset,
            },
        };

        setCoords(placements[arrowPosition] || placements.left);
    };

    useEffect(() => {
        const timer = setTimeout(() => setVisible(true), 100);
        calculatePosition();

        const target = document.querySelector(targetSelector);

        const handleTargetClick = (e) => {
            if (target) {
                target.click();
            }
            // handleClose();
        };

        let targetParent = null;
        if (target && target.parentNode) {
            targetParent = target.parentNode;
            targetParent.addEventListener('click', handleTargetClick);
        }

        window.addEventListener('resize', calculatePosition);
        window.addEventListener('scroll', calculatePosition, true);

        // Inject CSS styles for popover content
        const styleId = 'cq-popover-list-styles';
        let styleElement = document.getElementById(styleId);

        if (!styleElement) {
            styleElement = document.createElement('style');
            styleElement.id = styleId;
            document.head.appendChild(styleElement);
        }

        styleElement.textContent = `
            [id^="cq-popover-"] ul {
                margin: 8px 0;
                padding-left: 16px;
            }

            [id^="cq-popover-"] ul li {
                margin-bottom: 8px;
                line-height: 1.4;
            }

            [id^="cq-popover-"] ul li:last-child {
                margin-bottom: 0;
            }

            [id^="cq-popover-"] ul li strong {
                color: #1B463C;
            }
        `;

        return () => {
            clearTimeout(timer);
            window.removeEventListener('resize', calculatePosition);
            window.removeEventListener('scroll', calculatePosition, true);

            if (targetParent) {
                targetParent.removeEventListener('click', handleTargetClick);
            }

            // Clean up styles when popover is unmounted
            const style = document.getElementById(styleId);
            if (style) {
                style.remove();
            }
        };
    }, [targetSelector, arrowPosition]);


    const handleClose = () => {
        const overlay = document.getElementById(`cq-popover-overlay-${popoverId}`);
        if (overlay) overlay.remove();
        if (onClose) onClose();
    };

    const handleOverlayClick = (e) => {
        // Only close if clicking on the overlay background, not the popover content
        if (e.target.id === `cq-popover-overlay-${popoverId}`) {
            handleClose();
        }
    };

    const arrowStyles = {
        top: {
            position: 'absolute',
            bottom: '-10px',
            left: 'calc(50% - 10px)',
            borderLeft: '10px solid transparent',
            borderRight: '10px solid transparent',
            borderTop: '10px solid #E1F6CA',
        },
        topRight: {
            position: 'absolute',
            bottom: '-10px',
            right: '20px',
            borderLeft: '10px solid transparent',
            borderRight: '10px solid transparent',
            borderTop: '10px solid #E1F6CA',
        },
        bottom: {
            position: 'absolute',
            top: '-10px',
            left: 'calc(50% - 10px)',
            borderLeft: '10px solid transparent',
            borderRight: '10px solid transparent',
            borderBottom: '10px solid #E1F6CA',
        },
        bottomLeft: {
            position: 'absolute',
            top: '-10px',
            left: '20px',
            borderLeft: '10px solid transparent',
            borderRight: '10px solid transparent',
            borderBottom: '10px solid #E1F6CA',
        },
        left: {
            position: 'absolute',
            top: 'calc(50% - 10px)',
            right: '-10px',
            borderTop: '10px solid transparent',
            borderBottom: '10px solid transparent',
            borderLeft: '10px solid #E1F6CA',
        },
        leftBottom: {
            position: 'absolute',
            bottom: '20px',
            right: '-10px',
            borderTop: '10px solid transparent',
            borderBottom: '10px solid transparent',
            borderLeft: '10px solid #E1F6CA',
        },
        right: {
            position: 'absolute',
            top: 'calc(50% - 10px)',
            left: '-10px',
            borderTop: '10px solid transparent',
            borderBottom: '10px solid transparent',
            borderRight: '10px solid #E1F6CA',
        }
    };

    return (
        <div
            id={`cq-popover-overlay-${popoverId}`}
            onClick={handleOverlayClick}
            style={{
                position: 'fixed',
                top: 0,
                left: 0,
                width: '100vw',
                height: '100vh',
                backgroundColor: 'rgba(0, 0, 0, 0.2)',
                zIndex: 99998,
                pointerEvents: 'auto',
                fontFamily: "'Segoe UI', 'Roboto', sans-serif",
            }}
        >
            <div
                id={`cq-popover-${popoverId}`}
                ref={popoverRef}
                onClick={(e) => e.stopPropagation()}
                style={{
                    position: 'absolute',
                    top: coords.top,
                    left: coords.left,
                    backgroundColor: '#E1F6CA',
                    color: '#1B463C',
                    padding: '12px 16px',
                    borderRadius: '8px',
                    fontSize: '14px',
                    width: `${width}px`,
                    boxShadow: '0 2px 6px rgba(0,0,0,0.2)',
                    pointerEvents: 'auto',
                    opacity: visible ? 1 : 0,
                    transform: visible ? 'scale(1)' : 'scale(0.95)',
                    transition: 'opacity 300ms ease, transform 300ms ease',
                }}
            >
                {arrowStyles[arrowPosition] && <div style={arrowStyles[arrowPosition]} />}

                <button
                    onClick={handleClose}
                    style={{
                        position: 'absolute',
                        top: '13px',
                        right: '13px',
                        background: 'transparent',
                        border: 'none',
                        color: '#1B463C',
                        fontSize: '16px',
                        cursor: 'pointer',
                        lineHeight: '1',
                        padding: 0,
                    }}
                    aria-label="Close Popover"
                >
                    &times;
                </button>

                <span
                    style={{
                        display: 'block',
                        paddingRight: '1.5rem',
                        lineHeight: '1.5'
                    }}
                    dangerouslySetInnerHTML={{__html:message}}
                />
            </div>
        </div>
    );
};

export default Popover;
