import React, { useState, useEffect, useCallback, useMemo } from 'react';
import { icons } from '../../Constants/svg';
import Icon from '../UIComponents/Icon';
import InputBox from '../UIComponents/InputBox';
import TextAreaBox from '../UIComponents/TextAreaBox';
import Select from '../UIComponents/Select';
import { getActiveChatId, getContactName } from '../../utils/WppFunctions';
import { postWindowMessage } from '../../utils/messageSender';
import { isDarkMode } from '../../utils/utils';
import { formatDateTime } from '../../utils/dateUtils';
import { TRACK_EVENTS } from '../../Constants/constant';
import './Reminders.css';

const Reminders = ({ isOpen, chatId: initialChatId }) => {
  const isDark = isDarkMode();
  
  // Separate arrays like ChatTodo pattern
  const [pendingReminders, setPendingReminders] = useState([]);
  const [processedReminders, setProcessedReminders] = useState([]);
  const [currentChatId, setCurrentChatId] = useState(initialChatId);
  const [isLoading, setIsLoading] = useState(false);
  const [editingId, setEditingId] = useState(null);
  
  // Pagination state - separate loading states like ChatTodo
  const [pendingPage, setPendingPage] = useState(1);
  const [processedPage, setProcessedPage] = useState(1);
  const [isPendingLoadingMore, setIsPendingLoadingMore] = useState(false);
  const [isProcessedLoadingMore, setIsProcessedLoadingMore] = useState(false);
  const [hasMorePending, setHasMorePending] = useState(true);
  const [hasMoreProcessed, setHasMoreProcessed] = useState(true);
  const [pendingNextPageNumber, setPendingNextPageNumber] = useState(null);
  const [processedNextPageNumber, setProcessedNextPageNumber] = useState(null);
  
  // Collapsible sections state
  const [isPendingOpen, setIsPendingOpen] = useState(true);
  const [isProcessedOpen, setIsProcessedOpen] = useState(false);
  const [processedDataFetched, setProcessedDataFetched] = useState(false);
  
  const ITEMS_PER_PAGE = 10;
  
  // View state management like QuickReplyManager
  const [showForm, setShowForm] = useState(false);
  const [formError, setFormError] = useState('');
  
  // Form state
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    date: '',
    time: '',
    recurrence: '',
    recurrenceType: 'day'
  });
  
  const [isRepeat, setIsRepeat] = useState(false);

  const recurrenceOptions = ['day', 'week', 'month', 'year'];
  // const recurrenceOptions = ['minutes', 'hour', 'day', 'week', 'month', 'year'];

  useEffect(() => {
    const handleMessage = (event) => {
      if (event.source !== window) return;
      
      const { type, payload } = event.data;
      if ((type == 'CHAT_ARTIFACTS_UPDATED' || type == 'CHAT_ARTIFACT_UPDATE') && payload.chatId === currentChatId && payload.dataType === 'reminders') {
        handleRemindersUpdate(payload);
      }
    };

    window.addEventListener('message', handleMessage);
    return () => window.removeEventListener('message', handleMessage);
  }, [currentChatId, handleRemindersUpdate]);

  const handleRemindersUpdate = useCallback((payload) => {
    const reminders = payload.data;
    const nextUrl = payload.paginationMeta?.next;
    const requestStatus = payload.paginationMeta?.status;
    
    // Only update UI state if the panel is open (like ChatTodo)
    if (!isOpen || !Array.isArray(reminders)) return;
    
    const transformedReminders = reminders.map(reminder => {
        let reminderData = {};
        let metaData = {};
        
        // Try to parse main_content as JSON if it exists
        if (reminder.main_content && typeof reminder.main_content === 'string') {
          try {
            const parsed = JSON.parse(reminder.main_content);
            reminderData = { ...parsed };
          } catch (e) {
            // If parsing fails, use main_content as title
            reminderData.title = reminder.main_content;
          }
        }
        
        // Try to parse meta_data as JSON if it exists
        if (reminder.meta_data && typeof reminder.meta_data === 'string') {
          try {
            metaData = JSON.parse(reminder.meta_data);
          } catch (e) {
            // If parsing fails, treat as empty object
            metaData = {};
          }
        } else if (reminder.meta_data && typeof reminder.meta_data === 'object') {
          metaData = reminder.meta_data;
        }
        
        // Get status from reminder object (not from main_content)
        let status = reminder.status;
        if (!status) {
          // Fallback: determine status based on reminder date/time and recurrence
          const now = new Date();
          let reminderDate;
          
          if (reminderData.nextDue) {
            // For recurring reminders, use nextDue
            reminderDate = new Date(reminderData.nextDue);
          } else if (reminderData.date && reminderData.time) {
            // For one-time reminders, use date and time
            reminderDate = new Date(`${reminderData.date}T${reminderData.time}`);
          } else {
            // Fallback to current time if no valid date
            reminderDate = now;
          }
          
          status = reminderDate > now ? 'pending' : 'processed';
        }
        
        return {
          id: reminder.id,
          title: reminderData.title || 'Untitled',
          description: reminderData.description || '',
          date: reminderData.date,
          time: reminderData.time,
          recurrence: reminderData.recurrence || 0,
          recurrenceType: reminderData.recurrenceType || 'day',
          nextDue: reminderData.nextDue,
          contactName: metaData.contactName || 'Unknown Contact',
          // Snooze metadata if present
          snoozedAt: reminderData.snoozedAt,
          snoozedCount: reminderData.snoozedCount || 0,
          status: status,
          createdAt: reminder.created_at || reminder.lastUpdated,
          apiId: reminder.id
        };
      });
      
      // Determine status from pagination metadata first, then from data (like ChatTodo)
      const dataStatus = transformedReminders.length > 0 ? transformedReminders[0].status : null;
      const effectiveStatus = requestStatus || dataStatus;
      
      if (effectiveStatus === 'pending' || (!effectiveStatus && (transformedReminders.length === 0 || transformedReminders.every(r => r.status === 'pending')))) {
        // Handle pending reminders
        if (isPendingLoadingMore && pendingPage > 1) {
          setPendingReminders(prev => {
            const allReminders = [...prev, ...transformedReminders];
            const deduplicated = Array.from(new Map(allReminders.map(r => [r.apiId || r.id, r])).values());
            return deduplicated;
          });
        } else {
          setPendingReminders(transformedReminders);
          setPendingPage(1);
        }
        
        if (nextUrl) {
          const url = new URL(nextUrl);
          const pageFromUrl = parseInt(url.searchParams.get('page'), 10);
          setHasMorePending(true);
          setPendingNextPageNumber(pageFromUrl);
        } else {
          setHasMorePending(false);
          setPendingNextPageNumber(null);
        }
        setIsPendingLoadingMore(false);
      } else if (effectiveStatus === 'processed' || (transformedReminders.length > 0 && transformedReminders.every(r => r.status === 'processed'))) {
        // Handle processed reminders
        if (isProcessedLoadingMore && processedPage > 1) {
          setProcessedReminders(prev => {
            const allReminders = [...prev, ...transformedReminders];
            const deduplicated = Array.from(new Map(allReminders.map(r => [r.apiId || r.id, r])).values());
            return deduplicated;
          });
        } else {
          setProcessedReminders(transformedReminders);
          setProcessedPage(1);
        }
        
        if (nextUrl) {
          const url = new URL(nextUrl);
          const pageFromUrl = parseInt(url.searchParams.get('page'), 10);
          setHasMoreProcessed(true);
          setProcessedNextPageNumber(pageFromUrl);
        } else {
          setHasMoreProcessed(false);
          setProcessedNextPageNumber(null);
        }
        setIsProcessedLoadingMore(false);
        setProcessedDataFetched(true);
      } else {
        // Handle mixed data or backwards compatibility
        const pending = transformedReminders.filter(r => r.status === 'pending');
        const processed = transformedReminders.filter(r => r.status === 'processed');
        setPendingReminders(pending);
        setProcessedReminders(processed);
      }
    
    setIsLoading(false);
  }, []);

  useEffect(() => {
    getActiveChatId().then(id => {
      setCurrentChatId(id);
    });

    const handleChatChange = (chat) => {
      const chatId = chat?.id?._serialized;
      setCurrentChatId(chatId);
      setEditingId(null);
      resetForm();
      setShowForm(false);
      
      // Reset all data and pagination states for new chat - ChatTodo pattern
      setPendingReminders([]);
      setProcessedReminders([]);
      setPendingPage(1);
      setProcessedPage(1);
      setHasMorePending(false);
      setHasMoreProcessed(false);
      setPendingNextPageNumber(null);
      setProcessedNextPageNumber(null);
      setProcessedDataFetched(true); // Mark as fetched since we'll fetch both immediately
      setIsPendingLoadingMore(false);
      setIsProcessedLoadingMore(false);
    };

    window.WPP.on('chat.active_chat', handleChatChange);

    return () => {
      window.WPP.off('chat.active_chat', handleChatChange);
    };
  }, []);

  const fetchReminders = useCallback((chatId) => {
    if (!chatId) {
      setPendingReminders([]);
      setProcessedReminders([]);
      setIsLoading(false);
      return;
    }
    
    setIsLoading(true);
    setPendingPage(1);
    setProcessedPage(1);
    setHasMorePending(true);
    setHasMoreProcessed(true);
    setPendingReminders([]);
    setProcessedReminders([]);
    setIsPendingLoadingMore(false);
    setIsProcessedLoadingMore(false);
    
    // Fetch pending reminders
    postWindowMessage('CHAT_ARTIFACTS_FETCH', { 
      chatId: chatId, 
      data_type: "reminders",
      status: "pending",
      section: "pending",
      page: 1,
      page_size: ITEMS_PER_PAGE
    });
    
    // Fetch processed reminders
    postWindowMessage('CHAT_ARTIFACTS_FETCH', { 
      chatId: chatId, 
      data_type: "reminders",
      status: "processed",
      section: "processed",
      page: 1,
      page_size: ITEMS_PER_PAGE
    });
    
    // Fallback timeout to prevent infinite loading
    setTimeout(() => {
      setIsLoading(false);
      setIsPendingLoadingMore(false);
      setIsProcessedLoadingMore(false);
    }, 10000); // 10 seconds timeout
  }, []);
  
  useEffect(() => {
    if (currentChatId) {
      // Clear existing data and reset states - ChatTodo pattern
      setPendingReminders([]);
      setProcessedReminders([]);
      setPendingPage(1);
      setProcessedPage(1);
      setHasMorePending(false);
      setHasMoreProcessed(false);
      setPendingNextPageNumber(null);
      setProcessedNextPageNumber(null);
      setProcessedDataFetched(true); // Mark as fetched since we're fetching both
      setIsPendingLoadingMore(false);
      setIsProcessedLoadingMore(false);
      setIsLoading(true);
      
      // Fetch both pending and processed reminders immediately
      postWindowMessage('CHAT_ARTIFACTS_FETCH', { 
        chatId: currentChatId, 
        data_type: "reminders",
        status: "pending",
        section: "pending",
        page: 1,
        page_size: ITEMS_PER_PAGE
      });
      
      postWindowMessage('CHAT_ARTIFACTS_FETCH', { 
        chatId: currentChatId, 
        data_type: "reminders",
        status: "processed",
        section: "processed",
        page: 1,
        page_size: ITEMS_PER_PAGE
      });
    } else {
      // Clear data if no chat is selected
      setPendingReminders([]);
      setProcessedReminders([]);
      setIsLoading(false);
    }
  }, [currentChatId]);
  
  // Fetch processed reminders when processed section is expanded
  useEffect(() => {
    if (currentChatId && isProcessedOpen && !processedDataFetched) {
      setIsProcessedLoadingMore(true);
      setProcessedDataFetched(true);
      
      postWindowMessage('CHAT_ARTIFACTS_FETCH', { 
        chatId: currentChatId, 
        data_type: "reminders",
        status: "processed",
        section: "processed",
        page: 1,
        page_size: ITEMS_PER_PAGE
      });
    }
  }, [currentChatId, isProcessedOpen, processedDataFetched]);
  
  // Clear form errors when form fields change (time errors are handled separately)
  useEffect(() => {
    if (formError) {
      setFormError('');
    }
  }, [formData.title, formData.date, formData.time]);

  const resetForm = useCallback(() => {
    const now = new Date();
    now.setMinutes(now.getMinutes() + 1);
    const currentDate = now.toISOString().split('T')[0]; // YYYY-MM-DD format
    const currentTime = now.toTimeString().slice(0, 5); // HH:MM format
    
    setFormData({
      title: '',
      description: '',
      date: currentDate,
      time: currentTime,
      recurrence: '',
      recurrenceType: 'day'
    });
    setIsRepeat(false);
    setEditingId(null);
    setFormError('');
  }, []);

  const handleCancel = useCallback(() => {
    resetForm();
    setShowForm(false);
  }, [resetForm]);

  const handleInputChange = useCallback((field, value) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  }, []);

  const validateBasicForm = useCallback(() => {
    // Basic validation for button enablement
    return formData.title.trim() && formData.date && formData.time;
  }, [formData.title, formData.date, formData.time]);
  
  const validateTime = useCallback(() => {
    // Time validation for non-repeating reminders
    if (!isRepeat && formData.date && formData.time) {
      const reminderDateTime = new Date(`${formData.date}T${formData.time}`);
      const currentTime = new Date();
      
      if (reminderDateTime <= currentTime) {
        return 'Reminder time must be in the future for non-repeating reminders';
      }
    }
    
    return null;
  }, [formData.date, formData.time, isRepeat]);
  
  // Show error immediately when validation fails
  const currentTimeError = useMemo(() => {
    return validateTime();
  }, [validateTime]);
  
  // Button is enabled when basic form is valid (regardless of time error)
  const isButtonEnabled = useMemo(() => {
    return validateBasicForm();
  }, [validateBasicForm]);

  const calculateNextDue = (date, time, recurrence, recurrenceType) => {
    const dateTime = new Date(`${date}T${time}`);
    
    // For non-repeating reminders, return the original date/time
    if (!recurrence || recurrence === 0) {
      return dateTime.toISOString();
    }
    
    // For repeating reminders, calculate the next occurrence
    const now = new Date();
    let nextDue = new Date(dateTime);
    
    // If the reminder time has already passed, calculate the next occurrence
    while (nextDue <= now) {
      switch (recurrenceType) {
        case 'minutes':
          nextDue.setMinutes(nextDue.getMinutes() + recurrence);
          break;
        case 'hour':
          nextDue.setHours(nextDue.getHours() + recurrence);
          break;
        case 'day':
          nextDue.setDate(nextDue.getDate() + recurrence);
          break;
        case 'week':
          nextDue.setDate(nextDue.getDate() + (recurrence * 7));
          break;
        case 'month':
          nextDue.setMonth(nextDue.getMonth() + recurrence);
          break;
        case 'year':
          nextDue.setFullYear(nextDue.getFullYear() + recurrence);
          break;
        default:
          break;
      }
    }
    
    return nextDue.toISOString();
  };

  const handleSubmit = async () => {
    if (!currentChatId) return;
    
    // Clear previous form errors (time errors are shown separately)
    setFormError('');
    
    // Validate basic form fields
    if (!validateBasicForm()) {
      setFormError('Please fill in all required fields');
      return;
    }
    
    // Check if there's a time validation error
    if (currentTimeError) {
      // Don't proceed if there's a time error
      return;
    }

    setIsLoading(true);
    
    try {
      const recurrence = isRepeat ? parseInt(formData.recurrence) || 1 : 0;
      const recurrenceType = isRepeat ? formData.recurrenceType : 'day';
      
      // Get contact name for the current chat
      let contactName = '';
      try {
        contactName = await getContactName(currentChatId);
      } catch (error) {
        console.log('Could not get contact name, using default:', error);
      }
      
      // Prepare reminder data for main_content (without status)
      const reminderData = {
        title: formData.title.trim(),
        description: formData.description.trim(),
        date: formData.date,
        time: formData.time,
      };
      
      // Set nextDue and recurrence info (but not status in main_content)
      const currentTime = new Date().getTime();
      let reminderStatus;
      
      if (isRepeat && recurrence > 0) {
        const nextDue = calculateNextDue(formData.date, formData.time, recurrence, recurrenceType);
        const nextDueTime = new Date(nextDue).getTime();
        
        reminderData.recurrence = recurrence;
        reminderData.recurrenceType = recurrenceType;
        reminderData.nextDue = nextDue;
        reminderStatus = nextDueTime > currentTime ? 'pending' : 'processed';
      } else {
        // For one-time reminders, set status based on the reminder date/time
        const reminderDateTime = new Date(`${formData.date}T${formData.time}`);
        reminderData.nextDue = reminderDateTime.toISOString();
        reminderStatus = reminderDateTime.getTime() > currentTime ? 'pending' : 'processed';
      }

      // Create the main_content as a JSON string (without status)
      const main_content = JSON.stringify(reminderData);
      const meta_data = { contactName: contactName };

      if (editingId !== null) {
        postWindowMessage('CHAT_ARTIFACTS_UPDATE', {
          chatId: currentChatId,
          artifact_id: editingId,
          main_content,
          meta_data,
          data_type: 'reminders',
          status: reminderStatus
        });
        postWindowMessage('TRACK_EVENT', {
          eventName: TRACK_EVENTS.REMINDER_UPDATED,
          properties: { chatId: currentChatId, reminderId: editingId, status: reminderStatus }
        });
      } else {
        postWindowMessage('CHAT_ARTIFACTS_CREATE', {
          chatId: currentChatId,
          main_content,
          meta_data,
          data_type: 'reminders',
          status: reminderStatus
        });
        postWindowMessage('TRACK_EVENT', {
          eventName: TRACK_EVENTS.REMINDER_CREATED,
          properties: { chatId: currentChatId, status: reminderStatus }
        });
      }

      resetForm();
      setShowForm(false);
      
      // Note: No manual refresh - API response will update UI via message handler
    } catch (error) {
      console.error('Failed to save reminder:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleEdit = useCallback((reminder) => {
    setFormData({
      title: reminder.title,
      description: reminder.description,
      date: reminder.date,
      time: reminder.time,
      recurrence: reminder.recurrence.toString(),
      recurrenceType: reminder.recurrenceType
    });
    setIsRepeat(reminder.recurrence > 0);
    setEditingId(reminder.apiId || reminder.id);
    setShowForm(true);
  }, []);

  const handleDelete = useCallback((reminder) => {
    if (!currentChatId) return;
    if (!window.confirm('Are you sure you want to delete this reminder?')) return;

    setIsLoading(true);

    try {
      postWindowMessage('CHAT_ARTIFACTS_DELETE', {
        chatId: currentChatId,
        artifact_id: reminder.apiId || reminder.id,
        data_type: 'reminders'
      });
      postWindowMessage('TRACK_EVENT', {
        eventName: TRACK_EVENTS.REMINDER_DELETED,
        properties: { chatId: currentChatId, reminderId: reminder.apiId || reminder.id, status: reminder.status }
      });
      // Note: No manual refresh - API response will update UI via message handler
    } catch (error) {
      console.error('Failed to delete reminder:', error);
    } finally {
      setIsLoading(false);
    }
  }, [currentChatId]);
  
  const renderCollapsibleSection = useCallback((title, isOpen, setIsOpen, reminders, hasMore, isLoadingMore, loadMoreFunction) => {
    return (
      <div style={{
        marginBottom: '24px'
      }}>
        <div
          style={{
            fontSize: '16px',
            fontWeight: '600',
            color: isDark ? '#e9edef' : '#333',
            marginBottom: '12px',
            padding: '12px 16px',
            borderBottom: `1px solid ${isDark ? '#2a3942' : '#e0e0e0'}`,
            transition: 'color 0.2s ease, border-color 0.2s ease',
            userSelect: 'none',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'space-between'
          }}
          onClick={() => setIsOpen(!isOpen)}
        >
          <span>{title}</span>
          <span style={{ transform: isOpen ? 'rotate(180deg)' : 'rotate(0deg)', transition: 'transform 0.2s' }}>
            {icons.DOWN_ARROW}
          </span>
        </div>
        {isOpen && (
          <>
            <div style={{
              padding: '8px 0'
            }}>
              {reminders.length > 0 ? (
                reminders.map((reminder) => (
                  <ReminderCard
                    key={reminder.id}
                    reminder={reminder}
                    isDark={isDark}
                    onEdit={handleEdit}
                    onDelete={handleDelete}
                    isDisabled={isLoading || isPendingLoadingMore || isProcessedLoadingMore}
                    formatDateTime={formatDateTime}
                    formatRecurrence={formatRecurrence}
                  />
                ))
              ) : (
                <div style={{
                  padding: '20px',
                  textAlign: 'center',
                  color: isDark ? '#adb5bd' : '#666'
                }}>
                  {title === 'Pending' ? (
                    <>
                      <div style={{
                        fontSize: '18px',
                        fontWeight: '600',
                        marginBottom: '8px',
                        color: isDark ? '#e9edef' : '#333'
                      }}>No reminders yet</div>
                      <div style={{
                        fontSize: '14px',
                        lineHeight: '1.5',
                        maxWidth: '280px',
                        margin: '0 auto',
                        color: isDark ? '#adb5bd' : '#666'
                      }}>
                        Start adding reminders to never miss important tasks or events.
                      </div>
                    </>
                  ) : (
                    `No ${title.toLowerCase()} reminders`
                  )}
                </div>
              )}
            </div>
            {hasMore && reminders.length > 0 && (
              <div style={{ textAlign: 'center', padding: '20px' }}>
                <button 
                  onClick={loadMoreFunction}
                  disabled={isLoadingMore}
                  style={{
                    padding: '10px 20px',
                    backgroundColor: isDark ? '#00d9ff' : '#25d366',
                    color: isDark ? '#111b21' : 'white',
                    border: 'none',
                    borderRadius: '8px',
                    cursor: isLoadingMore ? 'not-allowed' : 'pointer',
                    fontSize: '14px',
                    fontWeight: '500',
                    opacity: isLoadingMore ? 0.6 : 1
                  }}
                >
                  {isLoadingMore ? 'Loading...' : `Load More ${title}`}
                </button>
              </div>
            )}
          </>
        )}
      </div>
    );
  }, [isDark, isLoading, handleEdit, handleDelete, formatDateTime, formatRecurrence]);

  const formatRecurrence = useCallback((recurrence, recurrenceType) => {
    if (!recurrence || recurrence === 0) return 'One-time';
    return `Every ${recurrence} ${recurrenceType}${recurrence > 1 ? 's' : ''}`;
  }, []);

  // Load more functions matching ChatTodo pattern
  const loadMorePendingReminders = useCallback(() => {
    if (!hasMorePending || isPendingLoadingMore || !pendingNextPageNumber) return;
    
    setIsPendingLoadingMore(true);
    setPendingPage(pendingNextPageNumber);
    
    postWindowMessage('CHAT_ARTIFACTS_FETCH', {
      chatId: currentChatId,
      data_type: "reminders",
      status: "pending",
      page: pendingNextPageNumber,
      page_size: ITEMS_PER_PAGE
    });
  }, [currentChatId, hasMorePending, isPendingLoadingMore, pendingNextPageNumber]);

  const loadMoreProcessedReminders = useCallback(() => {
    if (!hasMoreProcessed || isProcessedLoadingMore || !processedNextPageNumber) return;
    
    setIsProcessedLoadingMore(true);
    setProcessedPage(processedNextPageNumber);
    
    postWindowMessage('CHAT_ARTIFACTS_FETCH', {
      chatId: currentChatId,
      data_type: "reminders",
      status: "processed",
      page: processedNextPageNumber,
      page_size: ITEMS_PER_PAGE
    });
  }, [currentChatId, hasMoreProcessed, isProcessedLoadingMore, processedNextPageNumber]);

  // Arrays are already separated, no need for useMemo filters

  // Reusable expandable text component
  const ExpandableText = React.memo(({ text, isDark, maxLength = 100 }) => {
    const [isExpanded, setIsExpanded] = useState(false);

    if (!text || text.length <= maxLength) {
      return (
        <span style={{
          wordWrap: 'break-word',
          wordBreak: 'break-word',
          overflowWrap: 'anywhere',
          whiteSpace: 'pre-wrap'
        }}>
          {text}
        </span>
      );
    }

    return (
      <>
        <span style={{
          wordWrap: 'break-word',
          wordBreak: 'break-word',
          overflowWrap: 'anywhere',
          whiteSpace: 'pre-wrap'
        }}>
          {isExpanded ? text : `${text.slice(0, maxLength)}...`}
        </span>
        <button
          onClick={(e) => {
            e.stopPropagation();
            setIsExpanded(!isExpanded);
          }}
          style={{
            background: 'none',
            border: 'none',
            color: isDark ? '#00d9ff' : '#3b82f6',
            cursor: 'pointer',
            fontSize: 'inherit',
            fontWeight: '500',
            marginLeft: '4px',
            padding: '0',
            textDecoration: 'underline'
          }}
        >
          {isExpanded ? 'Show less' : 'Show more'}
        </button>
      </>
    );
  });

  const ExpandableDescription = React.memo(({ description, isDark }) => {
    const maxLength = 80; // Characters to show before truncating

    if (!description) return null;

    return (
      <div style={{
        fontSize: '12px',
        color: isDark ? '#adb5bd' : '#6b7280',
        marginBottom: '4px',
        lineHeight: '1.4',
        maxWidth: '100%'
      }}>
        <ExpandableText text={description} isDark={isDark} maxLength={maxLength} />
      </div>
    );
  });

  const ReminderCard = React.memo(({ reminder, isDark, onEdit, onDelete, isDisabled, formatDateTime, formatRecurrence }) => (
    <div style={{
      backgroundColor: isDark ? '#2a3942' : '#e8e8e8',
      padding: '12px',
      borderRadius: '8px',
      marginBottom: '10px',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: 'flex-start',
      color: isDark ? '#e9edef' : '#333',
      transition: 'background-color 0.2s ease, color 0.2s ease'
    }}>
      <div style={{ flex: 1, marginRight: '8px' }}>
        <div style={{
          fontWeight: '600',
          fontSize: '14px',
          color: isDark ? '#e9edef' : '#1f2937',
          marginBottom: '2px',
          lineHeight: '1.3',
          maxWidth: '100%'
        }}>
          <ExpandableText text={reminder.title} isDark={isDark} maxLength={60} />
        </div>
        {reminder.description && (
          <ExpandableDescription description={reminder.description} isDark={isDark} />
        )}
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          gap: '1px'
        }}>
          <div style={{
            fontSize: '11px',
            color: isDark ? '#9ca3af' : '#6b7280',
            display: 'flex',
            alignItems: 'center',
            gap: '4px'
          }}>
            <span style={{ display: 'inline-flex', alignItems: 'center' }}>{icons.CALENDAR}</span> {formatDateTime(reminder.date, reminder.time)}
          </div>
          <div style={{
            fontSize: '11px',
            color: isDark ? '#9ca3af' : '#6b7280',
            display: 'flex',
            alignItems: 'center',
            gap: '4px'
          }}>
            <span style={{ display: 'inline-flex', alignItems: 'center' }}>{icons.REPEAT}</span> {formatRecurrence(reminder.recurrence, reminder.recurrenceType)}
          </div>
          {reminder.status === 'pending' && reminder.snoozedCount > 0 && (
            <div style={{
              fontSize: '11px',
              color: isDark ? '#fbbf24' : '#f59e0b',
              display: 'flex',
              alignItems: 'center',
              gap: '4px'
            }}>
              <span style={{ display: 'inline-flex', alignItems: 'center' }}>{icons.SNOOZE}</span>
              Snoozed
            </div>
          )}
        </div>
      </div>
      <div style={{
        display: 'flex',
        gap: '4px',
        alignItems: 'center',
        flexShrink: 0
      }}>
        <Icon 
          icon={icons.EDIT} 
          tooltip="Edit Reminder" 
          label="Edit"
          onClick={() => onEdit(reminder)} 
          disabled={isDisabled}
          size="small"
        />
        <Icon 
          icon={icons.DELETE} 
          tooltip="Delete Reminder" 
          label="Delete"
          onClick={() => onDelete(reminder)}
          disabled={isDisabled}
          size="small"
        />
      </div>
    </div>
  ));

  if (!isOpen) return null;

  return (
    <div
      style={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%',
        overflow: 'hidden',
        backgroundColor: isDark ? '#1f2c33' : '#f0f2f5',
        padding: '0',
      }}
    >
      {currentChatId ? (
        <>
          {/* Alert header - always show when chat is selected */}
          <div className="cq-reminders-alert-header">
            <div className="cq-reminders-alert-status">
              <span className="cq-reminders-alert-icon">
                {icons.WARNING}
              </span>
              <span className="cq-reminders-alert-text">
                Reminders work only if WhatsApp Web is open.
              </span>
            </div>
          </div>

          {showForm ? (
            <div style={{ 
              padding: '20px',
              flex: 1,
              overflow: 'auto'
            }}>
              <div style={{
                display: 'flex',
                flexDirection: 'column',
                gap: '16px'
              }}>
                <div style={{
                  fontSize: '18px',
                  fontWeight: '600',
                  color: isDark ? '#e9edef' : '#333',
                  marginBottom: '8px'
                }}>
                  {editingId !== null ? 'Edit Reminder' : 'Create New Reminder'}
                </div>

                <div>
                  <InputBox
                    value={formData.title}
                    onChange={(e) => handleInputChange('title', e.target.value)}
                    placeholder="Reminder title..."
                    disabled={isLoading}
                  />
                </div>
                
                <div>
                  <TextAreaBox
                    value={formData.description}
                    onChange={(e) => handleInputChange('description', e.target.value)}
                    placeholder="Description (optional)..."
                    disabled={isLoading}
                  />
                </div>

                <div style={{ display: 'flex', gap: '8px' }}>
                  <InputBox
                    type="date"
                    value={formData.date}
                    onChange={(e) => handleInputChange('date', e.target.value)}
                    onClick={(e) => e.target.showPicker && e.target.showPicker()}
                    disabled={isLoading}
                    min={new Date().toISOString().split('T')[0]}
                  />
                  <InputBox
                    type="time"
                    value={formData.time}
                    onChange={(e) => handleInputChange('time', e.target.value)}
                    onClick={(e) => e.target.showPicker && e.target.showPicker()}
                    disabled={isLoading}
                    min={formData.date === new Date().toISOString().split('T')[0] ? new Date().toTimeString().slice(0, 5) : undefined}
                  />
                </div>

                <div style={{
                  display: 'flex',
                  alignItems: 'center',
                  gap: '8px',
                  padding: '4px 0'
                }}>
                  <input
                    type="checkbox"
                    id="repeat-checkbox"
                    checked={isRepeat}
                    onChange={(e) => {
                      setIsRepeat(e.target.checked);
                      if (!e.target.checked) {
                        // Clear recurrence fields when unchecked
                        setFormData(prev => ({
                          ...prev,
                          recurrence: '',
                          recurrenceType: 'day'
                        }));
                      }
                    }}
                    disabled={isLoading}
                    style={{
                      width: '16px',
                      height: '16px',
                      cursor: 'pointer'
                    }}
                  />
                  <label 
                    htmlFor="repeat-checkbox"
                    style={{
                      fontSize: '14px',
                      color: isDark ? '#e9edef' : '#333',
                      cursor: 'pointer',
                      userSelect: 'none'
                    }}
                  >
                    Repeat after
                  </label>
                </div>

                {isRepeat && (
                  <div style={{ display: 'flex', gap: '8px' }}>
                    <div style={{ width: '80px' }}>
                      <InputBox
                        type="number"
                        value={formData.recurrence}
                        onChange={(e) => handleInputChange('recurrence', e.target.value)}
                        placeholder="1"
                        disabled={isLoading}
                        min="1"
                      />
                    </div>
                    <div style={{ flex: 1 }}>
                      <Select
                        options={recurrenceOptions}
                        value={formData.recurrenceType}
                        onChange={(value) => handleInputChange('recurrenceType', value)}
                        placeholder="Select frequency"
                      />
                    </div>
                  </div>
                )}

                {(formError || currentTimeError) && (
                  <div style={{
                    padding: '12px',
                    backgroundColor: isDark ? '#2a1f1f' : '#ffebee',
                    border: `1px solid ${isDark ? '#5c1e1e' : '#e57373'}`,
                    borderRadius: '6px',
                    color: isDark ? '#ff6b6b' : '#d32f2f',
                    fontSize: '14px',
                    lineHeight: '1.4'
                  }}>
                    {formError || currentTimeError}
                  </div>
                )}

                <div style={{ display: 'flex', gap: '8px', marginTop: '16px' }}>
                  <button 
                    onClick={handleSubmit} 
                    style={{
                      flex: 1,
                      backgroundColor: isDark ? '#00d9ff' : '#007bff',
                      color: isDark ? '#111b21' : '#fff',
                      border: 'none',
                      borderRadius: '8px',
                      padding: '12px 16px',
                      fontSize: '14px',
                      fontWeight: '600',
                      cursor: 'pointer',
                      opacity: (!isButtonEnabled || isLoading) ? 0.5 : 1
                    }}
                    disabled={isLoading || !isButtonEnabled}
                  >
                    {isLoading ? '...' : editingId !== null ? 'Update' : 'Create'}
                  </button>

                  <button 
                    onClick={handleCancel} 
                    style={{
                      flex: 1,
                      backgroundColor: isDark ? '#2a3942' : '#6c757d',
                      color: '#fff',
                      border: 'none',
                      borderRadius: '8px',
                      padding: '12px 16px',
                      fontSize: '14px',
                      fontWeight: '600',
                      cursor: 'pointer'
                    }}
                    disabled={isLoading}
                  >
                    Cancel
                  </button>
                </div>
              </div>
            </div>
          ) : (
            <div style={{
              display: 'flex',
              flexDirection: 'column',
              height: '100%',
              overflow: 'hidden'
            }}>
              <div style={{
                flex: 1,
                overflow: 'auto',
                padding: '16px',
                paddingBottom: '100px',
                width: '100%',
                boxSizing: 'border-box'
              }}>
                {isLoading && pendingReminders.length === 0 && processedReminders.length === 0 ? (
                  <div style={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    padding: '40px 20px',
                    textAlign: 'center'
                  }}>
                    <div style={{
                      fontSize: '48px',
                      color: isDark ? '#6b7280' : '#999',
                      marginBottom: '16px',
                      opacity: 0.5
                    }}>
                      {icons.REMINDER}
                    </div>
                    <div style={{
                      fontSize: '18px',
                      fontWeight: '600',
                      color: isDark ? '#e9edef' : '#374151',
                      marginBottom: '8px'
                    }}>
                      Loading reminders...
                    </div>
                    <div style={{
                      fontSize: '14px',
                      color: isDark ? '#adb5bd' : '#6b7280',
                      lineHeight: '1.5'
                    }}>
                      Please wait while we load your reminders.
                    </div>
                  </div>
                ) : (
                  <>
                    {renderCollapsibleSection(
                      'Pending',
                      isPendingOpen,
                      setIsPendingOpen,
                      pendingReminders,
                      hasMorePending,
                      isPendingLoadingMore,
                      loadMorePendingReminders
                    )}
                    
                    {renderCollapsibleSection(
                      'Processed',
                      isProcessedOpen,
                      setIsProcessedOpen,
                      processedReminders,
                      hasMoreProcessed,
                      isProcessedLoadingMore,
                      loadMoreProcessedReminders
                    )}
                  </>
                )}
              </div>
              <div style={{
                position: 'absolute',
                bottom: 0,
                left: 0,
                right: 0,
                padding: '20px',
                borderTop: `1px solid ${isDark ? '#2a3942' : '#e0e0e0'}`,
                backgroundColor: isDark ? '#111b21' : '#f0f2f5',
                zIndex: 10
              }}>
                <button
                  onClick={() => {
                    setEditingId(null);
                    resetForm();
                    setShowForm(true);
                  }}
                  style={{
                    width: '100%',
                    backgroundColor: isDark ? '#00d9ff' : '#007bff',
                    color: isDark ? '#111b21' : '#fff',
                    border: 'none',
                    borderRadius: '8px',
                    padding: '12px 16px',
                    fontSize: '14px',
                    fontWeight: '600',
                    cursor: 'pointer',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    gap: '8px',
                    transition: 'all 0.2s ease',
                  }}
                  onMouseEnter={(e) => {
                    e.target.style.backgroundColor = isDark ? '#00c4e6' : '#0056b3';
                    e.target.style.transform = 'translateY(-1px)';
                    e.target.style.boxShadow = `0 4px 12px ${isDark ? 'rgba(0, 217, 255, 0.3)' : 'rgba(0, 123, 255, 0.3)'}`;
                  }}
                  onMouseLeave={(e) => {
                    e.target.style.backgroundColor = isDark ? '#00d9ff' : '#007bff';
                    e.target.style.transform = 'translateY(0)';
                    e.target.style.boxShadow = 'none';
                  }}
                >
                  <span style={{ fontSize: '16px', fontWeight: 'bold' }}>+</span>
                  Add Reminder
                </button>
              </div>
            </div>
          )}
        </>
      ) : (
        <div style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          padding: '40px 20px',
          textAlign: 'center',
          height: '100%'
        }}>
          <div style={{
            fontSize: '48px',
            color: isDark ? '#6b7280' : '#999',
            marginBottom: '20px',
            opacity: 0.5
          }}>
            {icons.REMINDER}
          </div>
          <div style={{
            fontSize: '18px',
            fontWeight: '600',
            color: isDark ? '#e9edef' : '#333',
            marginBottom: '8px'
          }}>
            Select Chat
          </div>
          <div style={{
            fontSize: '14px',
            color: isDark ? '#adb5bd' : '#666',
            lineHeight: '1.5',
            maxWidth: '280px'
          }}>
            Please select a chat to create and manage reminders.
          </div>
        </div>
      )}
    </div>
  );
};

export default Reminders;
