import React, { useState, useEffect, useRef } from 'react';
import { Flex, Button } from '@radix-ui/themes';
import { showSuggestionPreview } from '../../utils/showSuggestionPreview';
import { onBoarding } from '../../utils/onBoarding';
import { getActiveChatMessages } from '../../utils/WppFunctions';
import { postWindowMessage } from '../../utils/messageSender';
import { icons } from '../../Constants/svg.js';
import RephraseForm from '../../Components/RephraseForm/RephraseForm.js';
import { TRACK_EVENTS } from '../../Constants/constant.js';

const AiButton = () => {
  const [loading, setLoading] = useState(false);
  const [textAvailable, setTextAvailable] = useState(false);
  const [showRephraseForm, setShowRephraseForm] = useState(false);
  const formRef = useRef(null);

  const getEditableDiv = () =>
    document.querySelector('footer._ak1i .lexical-rich-text-input [contenteditable="true"]');

  const isInputText = () => {
    const editableDiv = getEditableDiv();
    return editableDiv?.innerText.trim() !== '';
  };

  const getInputText = () => getEditableDiv()?.innerText || '';

  useEffect(() => {
    const observerTarget = getEditableDiv();
    if (!observerTarget) return;

    const observer = new MutationObserver(() => {
      setTextAvailable(isInputText());
    });

    observer.observe(observerTarget, {
      childList: true,
      characterData: true,
      subtree: true,
    });

    setTextAvailable(isInputText());

    return () => observer.disconnect();
  }, []);

  const handleRehraseForm = (isOpened, isFormSubmitted=false) => {
    if (isOpened) {
      postWindowMessage('TRACK_EVENT', { eventName: TRACK_EVENTS.ADVANCE_GENERATE_FORM_OPENED });
      setShowRephraseForm(true);
    }
    else {
      if (isFormSubmitted) postWindowMessage('TRACK_EVENT', { eventName: TRACK_EVENTS.ADVANCE_GENERATE_FORM_CANCELLED });
      setShowRephraseForm(false);
    }
  };

  const handleClick = async () => {
    if (localStorage.getItem('IS_USER_ONBOARDED') !== 'true') {
      onBoarding();
      return;
    }

    const eventName = textAvailable ? TRACK_EVENTS.REPHRASE_BUTTON_CLICKED : TRACK_EVENTS.AI_REPLY_BUTTON_CLICKED;
    postWindowMessage('TRACK_EVENT', { eventName });

    const chatMessages = (await getActiveChatMessages()) || [];

    if (isInputText()) {
      chatMessages.rephrase_suggestion = {
        message: getInputText(),
        is_customer: false,
        name: 'you',
        timestamp: Date.now(),
      };
    }

    const getReply = textAvailable ? 'GET_REPHRASE_REPLY' : 'GET_AI_REPLY';

    postWindowMessage(getReply, chatMessages);
    showSuggestionPreview();
    setLoading(true);
  };

  useEffect(() => {
    const setAIButtonLoadingFalse = () => setLoading(false);
    const setAIButtonLoadingTrue = () => setLoading(true);

    window.addEventListener('SET_AIBUTTON_LOADING_FALSE', setAIButtonLoadingFalse);
    window.addEventListener('SET_AIBUTTON_LOADING_TRUE', setAIButtonLoadingTrue);
    return () => {
      window.addEventListener('SET_AIBUTTON_LOADING_TRUE', setAIButtonLoadingTrue);
      window.removeEventListener('SET_AIBUTTON_LOADING_FALSE', setAIButtonLoadingFalse);
    };
  }, []);

  const label = textAvailable ? 'Rephrase' : 'AI Reply';

  return (
    <>
      <Flex
        align="center"
        gap="0"
        style={{
          position: 'relative',
          zIndex: 10,
        }}
      >
        <Button
          onClick={handleClick}
          disabled={loading}
          variant="solid"
          color="blue"
          style={{
            cursor: loading ? 'not-allowed' : 'pointer',
            height: '30px',
            padding: '7px',
            borderRadius: textAvailable ? '6px' : '6px 0px 0px 6px',
          }}
        >
          {label}
        </Button>

        {!textAvailable && !loading && (
          <Button
            variant="solid"
            color="blue"
            style={{
              cursor: 'pointer',
              height: '30px',
              padding: '5px',
              marginRight: '5px',
              background: 'rgb(39, 106, 207)',
              borderRadius: '0px 6px 6px 0px',
            }}
            onClick={(e) => {
              e.stopPropagation();
              handleRehraseForm(true);
            }}
          >
            {icons.DROPDOWN_CHEVRON}
          </Button>
        )}
      </Flex>

      {showRephraseForm && (
        <>
          <div
            style={{
              position: 'fixed',
              top: 0,
              left: 0,
              width: '100vw',
              height: '100vh',
              backgroundColor: 'transparent',
              zIndex: 9998,
            }}
            onClick={() => handleRehraseForm(false)}
          />
          <div
            id="cq-rephrase-form"
            style={{
              position: 'absolute',
              bottom: '100%',
              right: '60px',
              marginBottom: '5px',
              zIndex: 9999,
            }}
          >
            <RephraseForm onCancel={() => handleRehraseForm(false, false)} label="Generate" />
          </div>
        </>
      )}
    </>
  );
};

export default AiButton;
