import { BaseRepository } from './BaseRepository.js';

/** Constants */
const DB_NAME = 'wawc';
const STORE_NAME = 'user';
const LAST_WID_KEY = 'last-wid-md';

/** WhatsApp ID Parser */
class WhatsAppIdParser {
  static parse(wid) {
    if (!wid) throw new Error('WhatsApp ID cannot be empty');

    const match = /^"?(\d+):[^@]+@c\.us"?$/.exec(String(wid).trim());
    if (!match) throw new Error(`Invalid WhatsApp ID format: ${wid}`);

    return match[1];
  }
}

/** Minimal User Repository */
export class UserRepository extends BaseRepository {
  constructor() {
    super(DB_NAME, STORE_NAME);
  }

  async getUserId() {
    return this.safeExecute(async () => {
      const record = await this.getByKey(LAST_WID_KEY);
      if (!record?.value) throw new Error('No user ID record found');
      return WhatsAppIdParser.parse(record.value);
    }, 'getUserId');
  }
}

export const userRepository = new UserRepository();
