import React from 'react';
import { Button } from '@radix-ui/themes';
import { getStaticMessage } from '../../utils/utils';
import { postWindowMessage } from '../../utils/messageSender';
import { TRACK_EVENTS } from '../../Constants/constant';
import './WelcomePage.css';

const WelcomePage = ({ onGetStarted }) => {
  
  const closeOnBoardingForm = () => {
    const onboarding = document.getElementById('cq-onboarding');
    onboarding?.remove();
    document.body.classList.remove('cq-body-class');
    postWindowMessage('TRACK_EVENT', { eventName: TRACK_EVENTS.WELCOME_PAGE_CANCELLED });
  };

  return (
    <div
      className='welcome-popup'
      style={{
        backgroundColor: '#fff',
        padding: '2rem',
        borderRadius: '12px',
        boxShadow: '0 8px 30px rgba(0,0,0,0.2)',
        minWidth: '350px',
        maxWidth: '400px',
        width: '100%',
        fontFamily: 'sans-serif',
        display: 'flex',
        flexDirection: 'column',
        color: '#1f2937',
      }}
    >
      <div dangerouslySetInnerHTML={{ __html: getStaticMessage('ONBOARDING_WELCOME_HEADLINE') || 'Welcome to The CQ' }} />
      <br />
      <div className='welcome-message' dangerouslySetInnerHTML={{ __html: getStaticMessage('ONBOARDING_WELCOME_MESSAGE') || '' }} />
      <div style={{ display: 'flex', justifyContent: 'flex-start', marginTop: '20px' }}>
        <Button
          variant="solid"
          color="blue"
          size="3"
          style={{ padding: '5px 16px', borderRadius: '5px', cursor: 'pointer' }}
          onClick={onGetStarted}
        >
          {getStaticMessage('ONBOARDING_WELCOME_CTA_TEXT') || 'Get Start'}
        </Button>
        <Button
          variant="solid"
          color="ghost"
          size="3"
          style={{ padding: '5px', borderRadius: '5px', cursor: 'pointer', marginLeft: '10px', color: '#1f2937' }}
          onClick={closeOnBoardingForm}
        >
          Later
        </Button>
      </div>
    </div>
  );
};

export default WelcomePage
