import React, { useEffect } from 'react';

const InputBox = ({ value, onChange, placeholder, disabled = false, type = 'text', name, onClick, ...props }) => {
  useEffect(() => {
    // Inject CSS styles for input
    const styleId = 'cq-input-styles';
    let styleElement = document.getElementById(styleId);
    
    if (!styleElement) {
      styleElement = document.createElement('style');
      styleElement.id = styleId;
      document.head.appendChild(styleElement);
    }

    styleElement.textContent = `
      .cq-input {
        width: 100%;
        padding: 8px;
        border: 1px solid #ccc;
        border-radius: 6px;
        background: #fff;
        color: #111b21;
        font-size: 14px;
        outline: none;
        margin-bottom: 4px;
        box-sizing: border-box;
        font-family: inherit;
        transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
      }

      .cq-input:disabled {
        background: #f5f5f5;
      }

      body.dark .cq-input {
        border: 1px solid #313d45;
        background: #2a3942;
        color: #e9edef;
      }

      body.dark .cq-input:disabled {
        background: #1e2a32;
      }

      .cq-input::placeholder {
        color: #667781;
        opacity: 1;
      }

      body.dark .cq-input::placeholder {
        color: #8696a0;
        opacity: 1;
      }

      .cq-input::-webkit-input-placeholder {
        color: #667781;
        opacity: 1;
      }

      body.dark .cq-input::-webkit-input-placeholder {
        color: #8696a0;
        opacity: 1;
      }

      .cq-input::-moz-placeholder {
        color: #667781;
        opacity: 1;
      }

      body.dark .cq-input::-moz-placeholder {
        color: #8696a0;
        opacity: 1;
      }

      .cq-input:-ms-input-placeholder {
        color: #667781;
        opacity: 1;
      }

      body.dark .cq-input:-ms-input-placeholder {
        color: #8696a0;
        opacity: 1;
      }

      /* Date and time input specific styles */
      .cq-input[type="date"], .cq-input[type="time"] {
        cursor: pointer;
      }

      .cq-input[type="date"]::-webkit-calendar-picker-indicator,
      .cq-input[type="time"]::-webkit-calendar-picker-indicator {
        cursor: pointer;
        opacity: 0.7;
      }

      .cq-input[type="date"]::-webkit-calendar-picker-indicator:hover,
      .cq-input[type="time"]::-webkit-calendar-picker-indicator:hover {
        opacity: 1;
      }
    `;
  }, []);

  return (
    <input
      name={name}
      type={type}
      value={value}
      disabled={disabled}
      onChange={onChange}
      onClick={onClick}
      placeholder={placeholder}
      className="cq-input"
      {...props}
    />
  );
};

export default InputBox;
