import { getActiveChatId, getUserTimezone, getActiveChatMessages } from './WppFunctions';


// Helper function to save summary to localStorage without rendering panel
export const saveSummaryToStorage = (text, chatIdOverride = null, lastMessageTimestamp) => {
  if (!text) return;

  (async () => {
    const timezone = await getUserTimezone();
    const activeChatId = chatIdOverride;

    // Create current datetime in user's timezone
    const currentDate = new Date();
    const currentDateTime = new Intl.DateTimeFormat('en-US', {
      timeZone: timezone,
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: false
    }).format(currentDate);

    // Save summary data to localStorage
    const summaryData = {
      last_generated_at: currentDateTime,
      summary: text,
      last_message_timestamp: lastMessageTimestamp
    };

    localStorage.setItem(`summary_${activeChatId}`, JSON.stringify(summaryData));
  })();
};

