  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  transition: background-color 0.2s ease;
}

body.dark .cq-chattodo-popup {
  background-color: #1f2c33;
  box-shadow: 2px 0 10px rgba(0,0,0,0.3);
}

.cq-chattodo-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.cq-chattodo-title {
  color: #333;
  margin: 0;
  transition: color 0.2s ease;
}

body.dark .cq-chattodo-title {
  color: #e9edef;
}

.cq-chattodo-close {
  background: none;
  border: none;
  font-size: 14px;
  color: #007bff;
  cursor: pointer;
  align-self: flex-end;
  transition: color 0.2s ease;
}

body.dark .cq-chattodo-close {
  color: #e9edef;
}

.cq-chattodo-container {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  padding-bottom: 170px;
}

.cq-chattodo-section {
  margin-bottom: 24px;
}

.cq-chattodo-section-header {
  font-size: 16px;
  font-weight: 600;
  color: #333;
  margin-bottom: 12px;
  padding: 12px 16px;
  border-bottom: 1px solid #e0e0e0;
  transition: color 0.2s ease, border-color 0.2s ease;
  user-select: none;
}

body.dark .cq-chattodo-section-header {
  color: #e9edef;
  border-bottom-color: #2a3942;
}

.cq-chattodo-section-content {
  padding: 8px 0;
}

.cq-chattodo-todo {
  background-color: #f0f0f0;
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  color: #333;
  transition: background-color 0.2s ease, color 0.2s ease;
}

body.dark .cq-chattodo-todo {
  background-color: #2a3942;
  color: #e9edef;
}

.cq-chattodo-content {
  flex: 1;
  margin-right: 10px;
}

.cq-chattodo-checkbox-wrapper {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 4px;
}

.cq-chattodo-checkbox {
  width: 20px;
  height: 20px;
  border: 2px solid #007bff;
  border-radius: 4px;
  background: transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  flex-shrink: 0;
  margin-top: 2px;
}

.cq-chattodo-checkbox:hover {
  background-color: rgba(0, 123, 255, 0.1);
}

.cq-chattodo-checkbox-checked {
  background-color: #007bff;
  color: white;
}

body.dark .cq-chattodo-checkbox {
  border-color: #00a884;
}

body.dark .cq-chattodo-checkbox:hover {
  background-color: rgba(0, 168, 132, 0.1);
}

body.dark .cq-chattodo-checkbox-checked {
  background-color: #00a884;
}

.cq-chattodo-text {
  line-height: 1.4;
  font-size: 14px;
  flex: 1;
}

.cq-chattodo-text-done {
  text-decoration: line-through;
  opacity: 0.6;
}

.cq-chattodo-input-fixed {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f0f2f5;
  border-top: 1px solid #e0e0e0;
  padding: 20px;
  z-index: 10;
}

body.dark .cq-chattodo-input-fixed {
  background-color: #111b21;
  border-top: 1px solid #2a3942;
}

.cq-chattodo-input {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.cq-chattodo-button {
  padding: 12px 20px;
  border-radius: 8px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  align-self: flex-end;
  transition: background-color 0.2s ease;
}

.cq-chattodo-button:hover {
  background-color: #0056b3;
}

.cq-chattodo-button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.cq-chattodo-placeholder {
  color: #333;
  transition: color 0.2s ease;
}

body.dark .cq-chattodo-placeholder {
  color: #e9edef;
}

.cq-chattodo-empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  color: #666;
  padding: 40px 20px;
}

body.dark .cq-chattodo-empty-state {
  color: #adb5bd;
}

.cq-chattodo-empty-icon {
  font-size: 48px;
  margin-bottom: 20px;
  opacity: 0.5;
  color: #999;
}

body.dark .cq-chattodo-empty-icon {
  color: #6b7280;
}

.cq-chattodo-empty-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
}

body.dark .cq-chattodo-empty-title {
  color: #e9edef;
}

.cq-chattodo-empty-description {
  font-size: 14px;
  line-height: 1.5;
  max-width: 280px;
  color: #666;
}

body.dark .cq-chattodo-empty-description {
  color: #adb5bd;
