import React, { useState, useRef, useEffect } from 'react';

const Select = ({ options, value, onChange, placeholder = "Select an option..." }) => {
  const [isOpen, setIsOpen] = useState(false);
  const dropdownRef = useRef(null);

  useEffect(() => {
    // Inject CSS styles for select dropdown
    const styleId = 'cq-select-styles';
    let styleElement = document.getElementById(styleId);
    
    if (!styleElement) {
      styleElement = document.createElement('style');
      styleElement.id = styleId;
      document.head.appendChild(styleElement);
    }

    styleElement.textContent = `
      .cq-select-box {
        padding: 8px;
        color: #111b21;
        border: 1px solid #ccc;
        border-radius: 6px;
        background: #fff;
        cursor: pointer;
        font-size: 14px;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
      }

      body.dark .cq-select-box {
        border: 1px solid #313d45;
        background: #2a3942;
        color: #e9edef;
      }

      .cq-select-placeholder {
        color: #667781;
      }

      body.dark .cq-select-placeholder {
        color: #8696a0;
      }

      .cq-select-dropdown {
        position: absolute;
        width: 100%;
        top: 110%;
        left: 0;
        background: #fff;
        color: #111b21;
        border: 1px solid #ccc;
        border-radius: 6px;
        max-height: 200px;
        overflow-y: auto;
        margin-top: 4px;
        z-index: 10;
        transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
      }

      body.dark .cq-select-dropdown {
        background: #2a3942;
        color: #e9edef;
        border: 1px solid #313d45;
      }

      .cq-select-option {
        padding: 8px 10px;
        cursor: pointer;
        font-size: 14px;
        background: white;
        font-weight: normal;
        transition: background-color 0.2s ease;
      }

      .cq-select-option.selected {
        background: #f0faff;
        font-weight: bold;
      }

      body.dark .cq-select-option {
        background: #2a3942;
      }

      body.dark .cq-select-option.selected {
        background: #313d45;
      }

      .cq-select-option:hover {
        background: #f5f5f5;
      }

      body.dark .cq-select-option:hover {
        background: #3c4a54;
      }
    `;
  }, []);

  const toggleDropdown = () => setIsOpen(!isOpen);

  const handleOptionClick = (option) => {
    onChange(option);
    setIsOpen(false);
  };

  const handleClickOutside = (event) => {
    if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
      setIsOpen(false);
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  return (
    <div ref={dropdownRef} style={{ position: 'relative', width: '100%' }}>
      {/* Display box */}
      <div
        onClick={toggleDropdown}
        className="cq-select-box"
      >
        {value ? (
          <span>{value}</span>
        ) : (
          <span className="cq-select-placeholder">{placeholder}</span>
        )}
      </div>

      {/* Dropdown menu */}
      {isOpen && (
        <div className="cq-select-dropdown">
          {options.map((option) => (
            <div
              key={option}
              onClick={() => handleOptionClick(option)}
              className={`cq-select-option ${option === value ? 'selected' : ''}`}
            >
              {option}
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Select;
