___CSS_LOADER_EXPORT___.push([module.id, `.cq-chatnotes-popup {
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  transition: background-color 0.2s ease;
}

body.dark .cq-chatnotes-popup {
  background-color: #1f2c33;
  box-shadow: 2px 0 10px rgba(0,0,0,0.3);
}

.cq-chatnotes-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.cq-chatnotes-title {
  color: #333;
  margin: 0;
  transition: color 0.2s ease;
}

body.dark .cq-chatnotes-title {
  color: #e9edef;
}

.cq-chatnotes-close {
  background: none;
  border: none;
  font-size: 14px;
  color: #007bff;
  cursor: pointer;
  align-self: flex-end;
  transition: color 0.2s ease;
}

body.dark .cq-chatnotes-close {
  color: #e9edef;
}

.cq-chatnotes-container {
  flex: 1;
  overflow-y: auto;
  padding: 20px;
  padding-bottom: 170px;
}

.cq-chatnotes-note {
  background-color: #f0f0f0;
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  color: #333;
  transition: background-color 0.2s ease, color 0.2s ease;
}

body.dark .cq-chatnotes-note {
  background-color: #2a3942;
  color: #e9edef;
}

.cq-chatnotes-content {
  flex: 1;
  margin-right: 10px;
}

.cq-chatnotes-text {
  margin-bottom: 4px;
  line-height: 1.4;
  font-size: 14px;
}

.cq-chatnotes-input-fixed {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #f0f2f5;
  border-top: 1px solid #e0e0e0;
  padding: 20px;
  z-index: 10;
}

body.dark .cq-chatnotes-input-fixed {
  background-color: #111b21;
  border-top: 1px solid #2a3942;
}

.cq-chatnotes-input {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.cq-chatnotes-button {
  padding: 12px 20px;
  border-radius: 8px;
  border: none;
  background-color: #007bff;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  align-self: flex-end;
  transition: background-color 0.2s ease;
}

.cq-chatnotes-button:hover {
  background-color: #0056b3;
}

.cq-chatnotes-placeholder {
  color: #333;
  transition: color 0.2s ease;
}

body.dark .cq-chatnotes-placeholder {
  color: #e9edef;
}

.cq-chatnotes-empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  color: #666;
  padding: 40px 20px;
}

body.dark .cq-chatnotes-empty-state {
  color: #adb5bd;
}

.cq-chatnotes-empty-icon {
  font-size: 48px;
  margin-bottom: 20px;
  opacity: 0.5;
  color: #999;
}

body.dark .cq-chatnotes-empty-icon {
  color: #6b7280;
}

.cq-chatnotes-empty-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
}

body.dark .cq-chatnotes-empty-title {
  color: #e9edef;
}

.cq-chatnotes-empty-description {
  font-size: 14px;
  line-height: 1.5;
  max-width: 280px;
  color: #666;
}

body.dark .cq-chatnotes-empty-description {
  color: #adb5bd;
}`, "",{"version":3,"sources":["webpack://./src/Components/ChatNotes/ChatNotes.css"],"names":[],"mappings":"AAAA;EACE,aAAa;EACb,sBAAsB;EACtB,YAAY;EACZ,kBAAkB;EAClB,sCAAsC;AACxC;;AAEA;EACE,yBAAyB;EACzB,sCAAsC;AACxC;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,SAAS;EACT,2BAA2B;AAC7B;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,gBAAgB;EAChB,YAAY;EACZ,eAAe;EACf,cAAc;EACd,eAAe;EACf,oBAAoB;EACpB,2BAA2B;AAC7B;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,aAAa;EACb,qBAAqB;AACvB;;AAEA;EACE,yBAAyB;EACzB,aAAa;EACb,kBAAkB;EAClB,mBAAmB;EACnB,aAAa;EACb,8BAA8B;EAC9B,uBAAuB;EACvB,WAAW;EACX,uDAAuD;AACzD;;AAEA;EACE,yBAAyB;EACzB,cAAc;AAChB;;AAEA;EACE,OAAO;EACP,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,kBAAkB;EAClB,SAAS;EACT,OAAO;EACP,QAAQ;EACR,yBAAyB;EACzB,6BAA6B;EAC7B,aAAa;EACb,WAAW;AACb;;AAEA;EACE,yBAAyB;EACzB,6BAA6B;AAC/B;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;AACX;;AAEA;EACE,kBAAkB;EAClB,kBAAkB;EAClB,YAAY;EACZ,yBAAyB;EACzB,WAAW;EACX,eAAe;EACf,eAAe;EACf,gBAAgB;EAChB,oBAAoB;EACpB,sCAAsC;AACxC;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,WAAW;EACX,2BAA2B;AAC7B;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,uBAAuB;EACvB,YAAY;EACZ,kBAAkB;EAClB,WAAW;EACX,kBAAkB;AACpB;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,mBAAmB;EACnB,YAAY;EACZ,WAAW;AACb;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,kBAAkB;EAClB,WAAW;AACb;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,gBAAgB;EAChB,WAAW;AACb;;AAEA;EACE,cAAc;AAChB","sourcesContent":[".cq-chatnotes-popup {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n  position: relative;\n  transition: background-color 0.2s ease;\n}\n\nbody.dark .cq-chatnotes-popup {\n  background-color: #1f2c33;\n  box-shadow: 2px 0 10px rgba(0,0,0,0.3);\n}\n\n.cq-chatnotes-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  margin-bottom: 20px;\n}\n\n.cq-chatnotes-title {\n  color: #333;\n  margin: 0;\n  transition: color 0.2s ease;\n}