import React, { useEffect } from 'react';

const Checkbox = ({ checked, onChange, id, label, labelColor = '#1f2937' }) => {
  useEffect(() => {
    // Inject CSS styles for checkbox
    const styleId = 'cq-checkbox-styles';
    let styleElement = document.getElementById(styleId);
    
    if (!styleElement) {
      styleElement = document.createElement('style');
      styleElement.id = styleId;
      document.head.appendChild(styleElement);
    }

    styleElement.textContent = `
      .cq-checkbox-label {
        display: flex;
        align-items: center;
        gap: 6px;
        cursor: pointer;
        font-size: 14px;
        color: #111b21;
        transition: color 0.2s ease;
      }

      body.dark .cq-checkbox-label {
        color: #e9edef;
      }

      .cq-checkbox-input {
        cursor: pointer;
        accent-color: #00a884;
      }

      body.dark .cq-checkbox-input {
        accent-color: #00d9ff;
      }
    `;
  }, []);

  return (
    <label htmlFor={id} className="cq-checkbox-label">
      <input 
        type="checkbox" 
        id={id}
        checked={checked}
        onChange={onChange}
        className="cq-checkbox-input"
      />
      {label}
    </label>  
  );
};

export default Checkbox;
