___CSS_LOADER_EXPORT___.push([module.id, `.cq-collapsible-sidebar {
  position: relative;
  top: 0;
  right: 0;
  height: 100%;
  width: 65px;
  z-index: 500;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  transition: transform 0.3s ease, opacity 0.3s ease;
  transform: translateX(0);
  opacity: 1;
  background-color: #f9f7f5;
  box-shadow: -2px 0 10px rgba(0,0,0,0.1);
  border-left: 1px solid #ddd;
}
.cq-sidebar-closed .cq-collapsible-sidebar{
  width: 0;
}

body.dark .cq-collapsible-sidebar {
  background-color: #111b21;
  box-shadow: -2px 0 10px rgba(0,0,0,0.3);
  border-left: 1px solid #2a3942;
}

.cq-collapsible-sidebar.closed {
  transform: translateX(100%);
  opacity: 0;
  pointer-events: none;
}

.cq-collapsible-sidebar.open {
  transform: translateX(0);
  opacity: 1;
  pointer-events: all;
}

.cq-sidebar-toggle {
  position: fixed;
  top: 5px;
  right: 5px;
  z-index: 510;
  transition: opacity 0.3s ease, top 0.3s;
}

.cq-sidebar-toggle.hidden {
  opacity: 0;
  pointer-events: none;
}

.cq-sidebar-toggle.visible {
  opacity: 1;
  pointer-events: all;
  top: 130px;
}

.cq-sidebar-toggle button {
  border: none;
  border-radius: 8px;
  padding: 10px;
  cursor: pointer;
  box-shadow: 0 0 6px rgba(0,0,0,0.2);
  transition: background-color 0.2s ease;
  background-color: #f0f0f0;
  color: #333;
}

body.dark .cq-sidebar-toggle button {
  background-color: #202c33;
  color: #e9edef;
}

.cq-sidebar-arrow-button {
  color: #333;
  transition: color 0.2s ease;
}

body.dark .cq-sidebar-arrow-button {
  color: #e9edef;
}
