___CSS_LOADER_EXPORT___.push([module.id, `.cq-reminders-popup {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  height: 100%;
  max-height: 600px;
  width: 100%;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.cq-reminders-container {
  flex: 1;
  overflow-y: auto;
  padding: 16px;
  min-height: 200px;
}

.cq-reminders-item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 12px;
  margin-bottom: 10px;
  background-color: #e8e8e8;
  border-radius: 8px;
  color: #333;
  transition: background-color 0.2s ease, color 0.2s ease;
}

.cq-reminders-content {
  flex: 1;
  margin-right: 12px;
}

.cq-reminders-title {
  font-weight: 600;
  font-size: 14px;
  color: #1f2937;
  margin-bottom: 4px;
}

.cq-reminders-description {
  font-size: 13px;
  color: #6b7280;
  margin-bottom: 8px;
  line-height: 1.4;
}

.cq-reminders-details {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.cq-reminders-datetime,
.cq-reminders-recurrence {
  font-size: 12px;
  color: #4b5563;
  display: flex;
  align-items: center;
  gap: 4px;
}

.cq-reminders-created {
  font-size: 11px;
  color: #9CA3AF;
  margin-top: 4px;
}

.cq-reminders-actions {
  display: flex;
  gap: 8px;
  align-items: flex-start;
}

.cq-reminders-empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 40px 20px;
  height: 100%;
  min-height: 200px;
}

.cq-reminders-empty-icon {
  width: 48px;
  height: 48px;
  margin-bottom: 16px;
  opacity: 0.3;
  color: #6b7280;
}

.cq-reminders-empty-icon svg {
  width: 100%;
  height: 100%;
}

.cq-reminders-empty-title {
  font-size: 18px;
  font-weight: 600;
  color: #374151;
  margin-bottom: 8px;
}

.cq-reminders-empty-description {
  font-size: 14px;
  color: #6b7280;
  line-height: 1.5;
  max-width: 300px;
}

.cq-reminders-form-fixed {
  border-top: 1px solid #e5e7eb;
  background: white;
  padding: 16px;
}

.cq-reminders-form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.cq-reminders-form-row {
  display: flex;
  gap: 8px;
}

.cq-reminders-form-row-split {
  display: flex;
  gap: 8px;
}

.cq-reminders-form-row-split > * {
  flex: 1;
}

.cq-reminders-button {
  background: #3b82f6;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 10px 16px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
}

.cq-reminders-button:hover:not(:disabled) {
  background: #2563eb;
}

.cq-reminders-button:disabled {
  background: #9ca3af;
  cursor: not-allowed;
}

.cq-reminders-button-secondary {
  background: #6b7280;
  margin-top: 8px;
}

.cq-reminders-button-secondary:hover:not(:disabled) {
  background: #4b5563;
}

/* Dark theme support */
[data-theme="dark"] .cq-reminders-popup {
  background: #1f2937;
  color: #f9fafb;
}

[data-theme="dark"] .cq-reminders-item {
  background-color: #2a3942;
  color: #e9edef;
}

body.dark .cq-reminders-item {
  background-color: #2a3942;
  color: #e9edef;
}

[data-theme="dark"] .cq-reminders-title {
  color: #f9fafb;
}

[data-theme="dark"] .cq-reminders-description {
  color: #d1d5db;
}

[data-theme="dark"] .cq-reminders-datetime,
[data-theme="dark"] .cq-reminders-recurrence {
  color: #d1d5db;
}

[data-theme="dark"] .cq-reminders-created {
  color: #9ca3af;
}

[data-theme="dark"] .cq-reminders-empty-title {
  color: #f9fafb;
}

[data-theme="dark"] .cq-reminders-empty-description {
  color: #d1d5db;
}

[data-theme="dark"] .cq-reminders-form-fixed {
  background: #1f2937;
  border-top-color: #374151;
}

/* Alert header styles matching ChatSummarizer */
.cq-reminders-alert-header {
  padding: 12px 16px;
  border-bottom: 1px solid #ffc107;
  background-color: #fff3cd;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  min-height: 30px;
}

body.dark .cq-reminders-alert-header {
  background-color: #3d3014;
  border-bottom-color: #ffc107;
}

.cq-reminders-alert-status {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.cq-reminders-alert-icon {
  font-size: 14px;
}

.cq-reminders-alert-text {
  font-size: 12px;
  color: #666;
}

body.dark .cq-reminders-alert-text {
  color: #adb5bd;
}
