import React, { useEffect } from 'react';
import ReactDOM from 'react-dom/client';
import Popover from './Popover';
import popoverManager from './popoverManager';
import { getStaticMessage } from '../../utils/utils';

// Constants
const POPOVER_CONTAINER_CLASS = 'cq-popover-wrapper';
const WELCOME_GUIDE_KEY = 'CQ_WELCOME_GUIDE';
const STEP_TRANSITION_DELAY = 100;
const DEFAULT_MAX_WAIT_MS = 10000;
const MESSAGE_TYPE_SHOW_POPOVER = 'SHOW_ONBOARDING_POPOVER';

// Onboarding flow configuration
const ONBOARDING_STEPS = {
  CHAT_SELECT: {
    id: 'onboarding-chat-select',
    message: () => getStaticMessage('GUIDE_CHAT_SELECT') || 'Select a chat where you want AI replies',
    targetSelector: '[aria-label="Chat list"] > :first-child',
    arrowPosition: 'right',
    nextStep: 'AI_GUIDE',
    maxWaitMs: 12000, // replaces (retries x delay)
  },
  AI_GUIDE: {
    id: 'ai-button-guide',
    message: () => getStaticMessage('GUIDE_AI_REPLY') || 'Click the AI button to generate a reply based on the conversation context.',
    targetSelector: '.cq-wrapper',
    arrowPosition: 'topRight',
    nextStep: 'SIDEBAR_GUIDE',
    maxWaitMs: 15000,
  },
  SIDEBAR_GUIDE: {
    id: 'sidebar-guide',
    message: () => getStaticMessage('GUIDE_SIDEBAR') || `
    <strong>The CQ: Step 3/3</strong><br><br>
    The CQ Bar brings missing power to your WhatsApp Conversation:<br><br>
    <ul>
      <li><strong>Quick Replies</strong> - Save your most-used responses and send them in one click or edit before sending.</li>
      <li><strong>Summarize This Chat</strong> - Generate 1-click chat summaries that highlight action items, reminders, and decisions.</li>
      <li><strong>Notes</strong> - Add private notes to any conversation to keep important details handy.</li>
      <li><strong>Reminders</strong> - Add reminders to any conversation to stay on top of your follow-ups.</li>
      <li><strong>To-Dos</strong> - Add a private to-do checklist to any conversation to keep track of your tasks.</li>
    </ul>
    `,
    targetSelector: '.cq-collapsible-sidebar div[style*="margin-bottom: 16px"] .cq-icon-container:first-child',
    arrowPosition: 'leftBottom',
    nextStep: null, // final step
    maxWaitMs: 8000,
  },
};

// Helper functions
const removeExistingPopoverContainer = () => {
  const existingContainer = document.querySelector(`.${POPOVER_CONTAINER_CLASS}`);
  if (existingContainer) {
    console.log('Removing existing popover container');
    existingContainer.remove();
  }
};

const createPopoverContainer = () => {
  const container = document.createElement('div');
  container.className = POPOVER_CONTAINER_CLASS;
  document.body.appendChild(container);
  return container;
};

const isWelcomeGuideActive = () => localStorage.getItem(WELCOME_GUIDE_KEY) === 'true';

const completeOnboarding = () => {
  localStorage.removeItem(WELCOME_GUIDE_KEY);
  removeExistingPopoverContainer();
  console.log('Onboarding guide completed - user marked as onboarded');
};

const waitForTarget = (selector, intervalMs = 1000) => {
  return new Promise((resolve) => {
    const check = () => {
      const el = document.querySelector(selector);
      if (el) {
        clearInterval(timer);
        resolve(el);
      }
    };
    const timer = setInterval(check, intervalMs);
    check(); // do an initial check right away
  });
}

export const OnboardingPopovers = () => {
  // Mount a popover and return a cleanup fn that unmounts + removes the container
  const mountPopover = (step) => {
    removeExistingPopoverContainer();

    const container = createPopoverContainer();
    const root = ReactDOM.createRoot(container);

    const handlePopoverClose = () => {
      popoverManager.unregisterPopover(step.id);

      // If this is the final step, mark onboarding as complete
      if (!step.nextStep) {
        completeOnboarding();
        return;
      }

      if (!isWelcomeGuideActive()) return;
      if (popoverManager.isPopoverActive(step.id)) {
        console.log(`Popover ${step.id} is already active`);
        return;
      }
      // Proceed to next step if welcome guide is still active
      setTimeout(() => {
        console.log('Proceeding to next step:', step.nextStep, new Date());
        window.postMessage({ type: MESSAGE_TYPE_SHOW_POPOVER, step: step.nextStep }, '*');
      }, STEP_TRANSITION_DELAY);
    };

    root.render(
        <Popover
          message={step.message()}
          targetSelector={step.targetSelector}
          arrowPosition={step.arrowPosition}
          onClose={handlePopoverClose}
          popoverId={step.id}
        />
    );
  };

  const showPopover = async (stepKey) => {
    const step = ONBOARDING_STEPS[stepKey];
    if (!step || !isWelcomeGuideActive()) {
      console.warn(`Invalid step or welcome guide inactive: ${stepKey}`);
      return;
    }

    if (popoverManager.isPopoverActive(step.id)) {
      console.log(`Popover ${step.id} is already active`);
      return;
    }

    try {
      // Wait for target element to appear
      await waitForTarget(step.targetSelector);

      // Close any existing popovers and mount the new one
      popoverManager.closeAllPopovers();
      popoverManager.registerPopover(step.id);
      mountPopover(step);
    } catch (error) {
      console.warn(`Popover ${step.id} skipped: ${error.message}`);

      // If there's a next step, proceed to it
      if (step.nextStep) {
        setTimeout(() => {
          window.postMessage({ type: MESSAGE_TYPE_SHOW_POPOVER, step: step.nextStep }, '*');
        }, STEP_TRANSITION_DELAY);
      }
    }
  };

  useEffect(() => {
    const handleMessage = (event) => {
      if (event.source !== window) return;

      const { type, step } = event.data || {};
      if (type === MESSAGE_TYPE_SHOW_POPOVER && step && isWelcomeGuideActive()) {
        showPopover(step);
      }
    };

    window.addEventListener('message', handleMessage);

    return () => {
      window.removeEventListener('message', handleMessage);
      popoverManager.closeAllPopovers();
    };
  }, []);

  return null;
};

export const mountOnboardingPopovers = () => {
  console.log('Mounting OnboardingPopovers component');
  // Check if already mounted
  if (document.getElementById('cq-onboarding-popovers')) return;

  // Create container for the onboarding popovers manager
  const container = document.createElement('div');
  container.id = 'cq-onboarding-popovers';
  container.style.display = 'none'; // Hidden component, only handles messages
  document.body.appendChild(container);

  const root = ReactDOM.createRoot(container);
  root.render(<OnboardingPopovers />);
};
