___CSS_LOADER_EXPORT___.push([module.id, `.cq-side-panel {
  height: 100vh;
  width: 100%;
  /* Match Reminders light background */
  background-color: #ffffff;
  border-left: 1px solid #ddd;
  display: flex;
  flex-direction: column;
  box-shadow: -2px 0 10px rgba(0, 0, 0, 0.1);
  position: relative;
}

body.dark .cq-side-panel {
  /* Use requested dark background */
  background-color: rgb(31, 44, 51);
  border-left: 1px solid #2a3942;
  box-shadow: -2px 0 10px rgba(0, 0, 0, 0.3);
}

.cq-side-panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  border-bottom: 1px solid #e0e0e0;
  background-color: #fff;
  min-height: 32px;
}

body.dark .cq-side-panel-header {
  background-color: #202c33;
  border-bottom: 1px solid #2a3942;
}

.cq-side-panel-title {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #333;
}

body.dark .cq-side-panel-title {
  color: #e9edef;
}

.cq-side-panel-close {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
  padding: 4px 8px;
  border-radius: 4px;
  transition: background-color 0.2s ease, color 0.2s ease;
  line-height: 1;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cq-side-panel-close:hover {
  background-color: #f0f0f0;
  color: #333;
}

body.dark .cq-side-panel-close {
  color: #adb5bd;
}

body.dark .cq-side-panel-close:hover {
  background-color: #374151;
  color: #e9edef;
}

.cq-side-panel-content {
  flex: 1;
  overflow-y: auto;
  padding: 0;
}

.cq-side-panel-content::-webkit-scrollbar {
  width: 6px;
}

.cq-side-panel-content::-webkit-scrollbar-track {
  background: transparent;
}

.cq-side-panel-content::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 3px;
}

body.dark .cq-side-panel-content::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.2);
}

.cq-side-panel-resizer {
  position: absolute;
  left: 0;
  top: 0;
  width: 5px;
  height: 100%;
  background-color: transparent;
  cursor: ew-resize;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.2s ease;
}

.cq-side-panel-resizer:hover {
  background-color: rgba(0, 123, 255, 0.1);
}

body.dark .cq-side-panel-resizer:hover {
  background-color: rgba(255, 255, 255, 0.05);
}

.cq-side-panel-resizer-dots {
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding: 2px 0;
}

.cq-resizer-dot {
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: #999;
  transition: background-color 0.2s ease;
}

.cq-side-panel-resizer:hover .cq-resizer-dot {
  background-color: #007bff;
}

body.dark .cq-resizer-dot {
  background-color: #6b7280;
}

body.dark .cq-side-panel-resizer:hover .cq-resizer-dot {
  background-color: #60a5fa;
}
