import React from 'react';
import ReactDOM from 'react-dom/client';
import ToastMessage from '../Components/ToastMessage/ToastMessage';

export function showToastMessage({ message, type = 'success', duration = 3000 }) {
  // Ensure one toast root
  let rootEl = document.getElementById('cq-toast-root');
  if (!rootEl) {
    rootEl = document.createElement('div');
    rootEl.id = 'cq-toast-root';
    document.body.appendChild(rootEl);
  }

  const root = ReactDOM.createRoot(rootEl);
  root.render(
    <ToastMessage
      message={message}
      type={type}
      duration={duration}
      onClose={() => {
        root.unmount();
        rootEl.remove();
      }}
    />
  );
}