import React from 'react';
import ReactDOM from 'react-dom/client';
import PreviewBox from '../Components/PreviewBox/PreviewBox';
import { getActiveChatMessages } from '../utils/WppFunctions';
import { postWindowMessage } from '../utils/messageSender';
import { REPLY_USED_MAPPING, REPLY_CANCEL_MAPPING } from '../Constants/constant';

export const showSuggestionPreview = (loading = true, suggestedText = '', requestId = '', loggedUserID = '', error = false, replyType='') => {
  const footer = document.querySelector('footer._ak1i');
  if (!footer) return;

  const old = document.getElementById('ai-suggestion-preview');
  if (old) old.remove();

  const container = document.createElement('div');
  container.id = 'ai-suggestion-preview';
  footer.insertBefore(container, footer.firstChild);
  const input = footer.querySelector('[contenteditable="true"]');

  const setInputText = (text) => {
    const isMac = /mac/i.test(navigator.userAgentData.platform) || /mac/i.test(navigator.userAgent.toLowerCase());
    input.focus();
    input.dispatchEvent(new KeyboardEvent('keydown', {
      key: 'a',
      code: 'KeyA',
      ctrlKey: !isMac,
      metaKey: isMac,
      bubbles: true,
    }));
    input.dispatchEvent(new KeyboardEvent('keyup', {
      key: 'a',
      code: 'KeyA',
      ctrlKey: !isMac,
      metaKey: isMac,
      bubbles: true,
    }));

    const event = new InputEvent('input', {
      bubbles: true,
      cancelable: true,
      data: text,
    });
    input.dispatchEvent(event);
  };



  const onInsert = () => {
    if (error) {
      container.remove();
      window.dispatchEvent(new CustomEvent('SET_AIBUTTON_LOADING_FALSE'));
      return;
    }
    if (input && suggestedText && !loading) {
      setInputText(suggestedText);
      container.remove();
      window.dispatchEvent(new CustomEvent('SET_AIBUTTON_LOADING_FALSE'));

      if (replyType) postWindowMessage('TRACK_EVENT', { eventName: REPLY_USED_MAPPING[replyType] });

      if (requestId) postWindowMessage('POST_REPLY_REQUEST_ID', { requestId: requestId, loggedUserID: loggedUserID });
    }
  };

  const onCancel = () => {
    container.remove();
    if (replyType) postWindowMessage('TRACK_EVENT', { eventName: REPLY_CANCEL_MAPPING[replyType] });
    window.dispatchEvent(new CustomEvent('SET_AIBUTTON_LOADING_FALSE'));
  };

  const onReload = async () => {
    showSuggestionPreview();
    const chatMessages = await getActiveChatMessages();
    if (suggestedText) {
      chatMessages.rephrase_suggestion = {
        message: suggestedText,
        is_customer: false,
        name: 'you',
        timestamp: Date.now(),
      }
    };
    postWindowMessage('GET_AI_RELOAD_REPLY', chatMessages);
  };

  const root = ReactDOM.createRoot(container);
  root.render(
    <PreviewBox
      text={suggestedText}
      loading={loading}
      onInsert={onInsert}
      onCancel={onCancel}
      onReload={onReload}
      requestId={requestId}
    />
  );
}