import React, { useEffect, useMemo, useState, useCallback, useRef } from 'react';
import { createPortal } from 'react-dom';
import './CustomerInfo.css';
import InputBox from '../UIComponents/InputBox';
import TextAreaBox from '../UIComponents/TextAreaBox';
import Checkbox from '../UIComponents/Checkbox';
import { getActiveChatNumber, getContactName, getActiveChatId, saveContactToPhone, isAddressBookContact, removeContactFromPhone } from '../../utils/WppFunctions';
import { postWindowMessage } from '../../utils/messageSender';
import { icons } from '../../Constants/svg';
import { closeSidePanel } from '../../utils/renderSidePanel';

const Section = ({ children, first = false }) => (
  <section className={first ? "cq-customer-section-first" : "cq-customer-section"}>
    {children}
  </section>
);

const Field = ({ label, children, full = false }) => (
  <label className={`cq-customer-field ${full ? 'cq-field-full' : ''}`}>
    <span className="cq-customer-label">{label}</span>
    {children}
  </label>
);

const CustomerInfo = ({ isOpen }) => {
  
  const [syncToPhone, setSyncToPhone] = useState(false);
  const [initialSyncToPhone, setInitialSyncToPhone] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [customerId, setCustomerId] = useState(null);
  const [currentChatId, setCurrentChatId] = useState(null);
  // Track only what's needed; remove unused loaded flag
  const [companies, setCompanies] = useState([]);
  const [companiesLoading, setCompaniesLoading] = useState(false);
  const [companySaving, setCompanySaving] = useState(false);
  const [companyError, setCompanyError] = useState('');
  const lastCompanyDraft = useRef(null);
  const [showCompanyDropdown, setShowCompanyDropdown] = useState(false);
  const [companiesPage, setCompaniesPage] = useState(1);
  const [hasMoreCompanies, setHasMoreCompanies] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [searchTimeout, setSearchTimeout] = useState(null);
  
  // Single selected company is tracked in `selectedCompanyToEdit`

  const [form, setForm] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    street: '',
    city: '',
    state: '',
    country: '',
    leadSource: '',
    relationshipType: '',
    companyName: '',
    companyDomain: '',
    companyIndustry: '',
    companyQuery: '',
    designation: ''
  });

  const [initialForm, setInitialForm] = useState(form);
  const [showCompanySection, setShowCompanySection] = useState(false);
  const [firstNameError, setFirstNameError] = useState('');
  const [showSuccessToast, setShowSuccessToast] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');
  const [isAddressCollapsed, setIsAddressCollapsed] = useState(true);
  const [selectedCompanyToEdit, setSelectedCompanyToEdit] = useState(null);
  const [isEditMode, setIsEditMode] = useState(false);
  const [isPhoneBookContact, setIsPhoneBookContact] = useState(false);

  // Derived flags
  const isGroupChat = currentChatId ? (currentChatId.endsWith('@g.us') || currentChatId.endsWith('@newsletter')): false;
  const hasCustomerData = customerId || isPhoneBookContact;
  const shouldShowCard = hasCustomerData && !isEditMode;

  const setCustomerDataFromAPI = useCallback((customerData) => {
    const customerForm = {
      firstName: customerData.first_name || '',
      lastName: customerData.last_name || '',
      email: customerData.email || '',
      phone: customerData.phone || '',
      street: customerData.street_address || '',
      city: customerData.city || '',
      state: customerData.state || '',
      country: customerData.country || '',
      leadSource: customerData.source || '',
      relationshipType: '',
      companyName: '',
      companyDomain: '',
      companyIndustry: '',
      companyQuery: '',
      designation: ''
    };

    // Map selected company from API response (new API returns single `company`)
    if (customerData.company) {
      const company = customerData.company;
      // Reflect selected company in the inline field and designation
      customerForm.companyQuery = company.name || '';
      customerForm.relationshipType = company.relationship_type || '';
      customerForm.designation = company.relationship_type || '';
      setSelectedCompanyToEdit(company);
    } else {
      setSelectedCompanyToEdit(null);
    }

    setForm(customerForm);
    setInitialForm(customerForm);
    setCustomerId(customerData.id);
    setShowCompanySection(false);
    // Set sync-to-phone flag from API response
    const syncVal = customerData.sync_to_phone === true;
    setSyncToPhone(syncVal);
    setInitialSyncToPhone(syncVal);
    // Exit edit mode when data is loaded
    setIsEditMode(false);
  }, []);

  const saveCustomerIdToStorage = useCallback((chatId, customerId) => {
    try {
      const customerIds = JSON.parse(localStorage.getItem('customerIds') || '{}');
      customerIds[chatId] = customerId;
      localStorage.setItem('customerIds', JSON.stringify(customerIds));
    } catch (e) {
      console.error('Error saving customer ID to localStorage:', e);
    }
  }, []);

  const getCustomerIdFromStorage = useCallback((chatId) => {
    try {
      const customerIds = JSON.parse(localStorage.getItem('customerIds') || '{}');
      return customerIds[chatId] || null;
    } catch (e) {
      console.error('Error getting customer ID from localStorage:', e);
      return null;
    }
  }, []);

  const removeCustomerIdFromStorage = useCallback((chatId) => {
    try {
      const customerIds = JSON.parse(localStorage.getItem('customerIds') || '{}');
      if (customerIds && chatId in customerIds) {
        delete customerIds[chatId];
        localStorage.setItem('customerIds', JSON.stringify(customerIds));
      }
    } catch (e) {
      console.error('Error removing customer ID from localStorage:', e);
    }
  }, []);

  const handleMessage = useCallback((event) => {
    if (event.source !== window) return;
    
    const { type, payload } = event.data;
    const { actionType, data } = payload || {};
    
    if (type === 'CUSTOMER_UPDATE') {
      // Only update UI state when panel is open, mirroring other components
      if (!isOpen) return;
      if (actionType === 'CUSTOMER_RETRIEVED' && data) {
        // Ignore stale responses for previous chat
        if (data.chat_id && currentChatId && data.chat_id !== currentChatId) {
          return;
        }
        setCustomerDataFromAPI(data);
        setIsLoading(false);
        // no-op: when loading an existing customer we already show fresh data
      } else if (actionType === 'CUSTOMER_RETRIEVED' && !data) {
        // Failure or no data: stop loading and ensure form reflects initial chat-derived values
        setIsLoading(false);
        setCustomerId(null);
        setSelectedCompanyToEdit(null);
        setShowCompanySection(false);
        setForm(prev => ({ ...initialForm }));
        setSyncToPhone(false);
        setInitialSyncToPhone(false);
      } else if (actionType === 'CUSTOMER_CREATED' && data) {
        const newId = data.customer_id || data.id;
        if (newId) {
          setCustomerId(newId);
          saveCustomerIdToStorage(currentChatId, newId);
        }
        // Keep current form as-is and show success toast
        setInitialForm(prev => ({ ...form }));
        setInitialSyncToPhone(syncToPhone);
        setIsLoading(false);
        setIsEditMode(false); // Exit edit mode to show card
        setSuccessMessage(data.message || 'Saved successfully.');
        setShowSuccessToast(true);
        setTimeout(() => setShowSuccessToast(false), 2000);
      } else if (actionType === 'CUSTOMER_UPDATED' && data) {
        const updatedId = data.customer_id || data.id || customerId;
        if (updatedId && updatedId !== customerId) {
          setCustomerId(updatedId);
          saveCustomerIdToStorage(currentChatId, updatedId);
        }
        // Keep current form as-is and show success toast
        setInitialForm(prev => ({ ...form }));
        setInitialSyncToPhone(syncToPhone);
        setIsLoading(false);
        setIsEditMode(false); // Exit edit mode to show card
        setSuccessMessage(data.message || 'Saved successfully.');
        setShowSuccessToast(true);
        setTimeout(() => setShowSuccessToast(false), 2000);
      } else if (actionType === 'CUSTOMER_DELETED') {
        // On delete success, remove from local storage and WhatsApp
        if (data) {
          removeCustomerIdFromStorage(currentChatId);
        }
        // Keep name and phone like an unsaved contact; clear the rest
      }
    }

    if (type === 'COMPANIES_UPDATE') {
      if (!isOpen) return;
      if (actionType === 'COMPANIES_RETRIEVED' && data) {
        const { results, next } = data;
        if (companiesPage === 1) {
          setCompanies(results || []);
        } else {
          setCompanies(prev => [...prev, ...(results || [])]);
        }
        setHasMoreCompanies(!!next);
        setCompaniesLoading(false);
      } else if ((actionType === 'COMPANY_CREATED' || actionType === 'COMPANY_UPDATED') && data) {
        const draft = lastCompanyDraft.current || getDraftCompanyFromForm();
        const updatedCompany = { id: data.company_id, ...draft };
        applyCompanySuccess(updatedCompany, data.message);
        lastCompanyDraft.current = null;
      } else if (actionType === 'COMPANY_ERROR' && data) {
        setCompanySaving(false);
        setCompanyError(data.error || 'Failed to save company');
      }
    }
  }, [currentChatId, companiesPage, setCustomerDataFromAPI, saveCustomerIdToStorage, isOpen, initialForm]);

  useEffect(() => {
    window.addEventListener('message', handleMessage);
    return () => window.removeEventListener('message', handleMessage);
  }, [handleMessage]);

  const setFromChat = useCallback(async () => {
    try {
      const active = await window.WPP?.chat?.getActiveChat();
      const chatId = active?.id?._serialized;
      const number = await getActiveChatNumber();
      let name = '';
      
      setCurrentChatId(chatId);
      
      if (chatId) {
        try {
          name = await getContactName(chatId, true);
        } catch {}
        
        // Check if we have a stored customer ID for this chat
        const storedCustomerId = getCustomerIdFromStorage(chatId);
        if (storedCustomerId) {
          setCustomerId(storedCustomerId);
        }        
      }
      
      const parts = (name || '').trim().split(/\s+/);
      const firstName = parts[0] || '';
      const lastName = parts.slice(1).join(' ') || '';

      // Ensure a leading '+' when pre-filling from chat (used when API has no customer)
      const ensurePlus = (val) => {
        if (!val) return val;
        return String(val).startsWith('+') ? String(val) : `+${val}`;
      };
      const next = { ...form, firstName, lastName, phone: ensurePlus(number) };
      setForm(next);
      setInitialForm(next);
    } catch (e) {
      setIsLoading(false);
      // ignore
    }
  }, [getCustomerIdFromStorage]);

  // Initialize from current chat on mount and listen to chat changes
  useEffect(() => {
    getActiveChatId().then(id => {
      if (id) {
        setCurrentChatId(id);
        setFromChat();
      } else {
        // No active chat selected
        setCurrentChatId(null);
        resetAllState();
      }
    });

    const handleChatChange = (chat) => {
      const chatId = chat?.id?._serialized;
      if (chatId && chatId !== currentChatId) {
        // First set the new chat id so fetch effect uses the correct one
        setCurrentChatId(chatId);
        // Reset state and then prefill from the new chat
        resetAllState();
        setTimeout(() => {
          setFromChat();
        }, 10);
      } else if (!chatId) {
        // No chat selected now
        setCurrentChatId(null);
        resetAllState();
      }
    };

    if (window.WPP) {
      window.WPP.on('chat.active_chat', handleChatChange);
    }

    return () => {
      if (window.WPP) {
        window.WPP.off('chat.active_chat', handleChatChange);
      }
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // Safety net: poll for active chat so that when the user navigates
  // to a WA screen without an active chat, we immediately show the
  // empty state instead of leaving the form/card visible.
  useEffect(() => {
    if (!isOpen) return;

    const check = async () => {
      try {
        const id = await getActiveChatId();
        if (!id && currentChatId) {
          setCurrentChatId(null);
          resetAllState();
        } else if (id && id !== currentChatId) {
          setCurrentChatId(id);
          resetAllState();
          // Prefill from the newly active chat
          setTimeout(() => {
            setFromChat();
          }, 10);
        }
      } catch (_) {
        // ignore polling errors
      }
    };

    // Run immediately for instant reaction
    check();
    // Tight interval for near-instant detection
    const interval = setInterval(check, 250);
    return () => clearInterval(interval);
  }, [isOpen, currentChatId, resetAllState, setFromChat]);

  // Helper function to reset all state
  const resetAllState = useCallback(() => {
    const emptyForm = {
      firstName: '',
      lastName: '',
      email: '',
      phone: '',
      street: '',
      city: '',
      state: '',
      country: '',
      leadSource: '',
      relationshipType: '',
      companyName: '',
      companyDomain: '',
      companyIndustry: '',
      companyQuery: '',
      designation: ''
    };

    setForm(emptyForm);
    setInitialForm(emptyForm);
    setSelectedCompanyToEdit(null);
    setCustomerId(null);
    setShowCompanySection(false);
    setSyncToPhone(false);
    setInitialSyncToPhone(false);
    setIsLoading(false);
    setFirstNameError('');
    setShowSuccessToast(false);
    setIsEditMode(false);
    setIsPhoneBookContact(false);
  }, []);

  // Removed duplicate chat change effect to avoid double triggers

  // Fetch customer data when chat changes only
  useEffect(() => {
    if (currentChatId && !isGroupChat) {
      setIsLoading(true);
      postWindowMessage('CUSTOMER_GET', { chat_id: currentChatId });

      // Check if contact is in phone book
      isAddressBookContact(currentChatId)
        .then(isInPhoneBook => {
          setIsPhoneBookContact(isInPhoneBook);
        })
        .catch(() => {
          setIsPhoneBookContact(false);
        });
    }
  }, [currentChatId, isGroupChat]);

  const isDirty = useMemo(() => {
    if (isLoading) return false;
    const keys = Object.keys(initialForm);
    const formChanged = keys.some(k => (form[k] || '') !== (initialForm[k] || ''));
    const syncChanged = syncToPhone !== initialSyncToPhone;
    return formChanged || syncChanged;
  }, [form, initialForm, syncToPhone, initialSyncToPhone, isLoading]);

  if (!isOpen) return null;

  const update = (key) => (e) => {
    const value = e?.target ? e.target.value : e;
    setForm(prev => ({ ...prev, [key]: value }));
    if (key === 'firstName') setFirstNameError('');
  };

  const fetchCompanies = (query = '', page = 1) => {
    setCompaniesLoading(true);
    setCompaniesPage(page);
    postWindowMessage('COMPANIES_GET', {
      name: query,
      page,
      page_size: 10
    });
  };

  const handleCompanySearch = (e) => {
    const query = e.target.value;
    setForm(prev => ({ ...prev, companyQuery: query }));
    setSearchQuery(query);
    
    // Clear previous timeout
    if (searchTimeout) {
      clearTimeout(searchTimeout);
    }
    
    // Set new timeout for debounced search
    const newTimeout = setTimeout(() => {
      fetchCompanies(query, 1);
    }, 300);
    
    setSearchTimeout(newTimeout);
    setShowCompanyDropdown(true);
  };

  const handleCompanyInputFocus = () => {
    if (!showCompanyDropdown && companies.length === 0) {
      fetchCompanies('', 1);
    }
    setShowCompanyDropdown(true);
  };

  // Helpers for company actions
  const getDraftCompanyFromForm = useCallback(() => {
    return {
      name: (form.companyName && form.companyName.trim()) ? form.companyName.trim() : (form.companyQuery || '').trim(),
      domain: form.companyDomain || '',
      industry: form.companyIndustry || ''
    };
  }, [form.companyName, form.companyQuery, form.companyDomain, form.companyIndustry]);

  const applyCompanySuccess = useCallback((company, message) => {
    setCompanies(prev => {
      const idx = prev.findIndex(c => c.id === company.id);
      if (idx >= 0) {
        const copy = [...prev];
        copy[idx] = { ...copy[idx], ...company };
        return copy;
      }
      return [company, ...prev];
    });

    setSelectedCompanyToEdit(prev => ({ ...(prev || {}), ...company }));
    setForm(prev => ({ ...prev, companyQuery: company.name }));

    setShowCompanyDropdown(false);
    setCompanySaving(false);
    setShowCompanySection(false);
    setCompanyError('');

    setSuccessMessage(message || 'Company saved successfully.');
    setShowSuccessToast(true);
    setTimeout(() => setShowSuccessToast(false), 2000);
  }, []);

  const handleCompanySelect = (company) => {
    setForm(prev => ({
      ...prev,
      companyQuery: company.name,
      companyName: company.name,
      companyDomain: company.domain,
      companyIndustry: company.industry,
      relationshipType: company.relationship_type || '',
      designation: company.relationship_type || ''
    }));
    setShowCompanyDropdown(false);
    // Show the selected company with edit button instead of opening form
    setSelectedCompanyToEdit(company);
    setCompanyError('');
  };

  const loadMoreCompanies = () => {
    if (!companiesLoading && hasMoreCompanies) {
      fetchCompanies(searchQuery, companiesPage + 1);
    }
  };

  // Close dropdown when clicking outside
  const handleClickOutside = (e) => {
    if (!e.target.closest('.company-search-container')) {
      setShowCompanyDropdown(false);
    }
  };

  React.useEffect(() => {
    document.addEventListener('click', handleClickOutside);
    return () => document.removeEventListener('click', handleClickOutside);
  }, []);

  // Helper to clear company form fields
  const clearCompanyForm = () => {
    setForm(prev => ({
      ...prev,
      companyName: '',
      companyDomain: '',
      companyIndustry: '',
      relationshipType: '',
      designation: ''
    }));
  };

  // Company API actions
  const createCompany = () => {
    const name = (form.companyName && form.companyName.trim()) ? form.companyName.trim() : (form.companyQuery || '').trim();
    if (!name) return;
    setCompanySaving(true);
    setCompanyError('');
    const domain = form.companyDomain || '';
    const industry = form.companyIndustry || '';
    lastCompanyDraft.current = { name, domain, industry };
    postWindowMessage('COMPANY_CREATE', { name, domain, industry });
  };

  const updateCompany = () => {
    if (!selectedCompanyToEdit?.id) return;
    const name = (form.companyName && form.companyName.trim()) ? form.companyName.trim() : (form.companyQuery || '').trim();
    if (!name) return;
    setCompanySaving(true);
    setCompanyError('');
    const domain = form.companyDomain || '';
    const industry = form.companyIndustry || '';
    lastCompanyDraft.current = { name, domain, industry };
    postWindowMessage('COMPANY_UPDATE', {
      company_id: selectedCompanyToEdit.id,
      name,
      domain,
      industry
    });
  };

  // Removed legacy multi-company handlers (no longer used)

  // Handle cancel for company form
  const handleCompanyFormCancel = () => {
    // Do not alter the current associated company shown in the main field
    // Simply close the form and clear editable fields
    clearCompanyForm();
    setCompanySaving(false);
    setCompanyError('');
    setShowCompanySection(false);
  };

  const handleSave = async () => {
    if (!currentChatId) return;
    
    // Validate mandatory fields
    if (!form.firstName || !form.firstName.trim()) {
      setFirstNameError('First name is mandatory');
      return;
    } else {
      setFirstNameError('');
    }

    setIsLoading(true);
    
    // Build payload aligned with new API
    const hasCompany = !!(selectedCompanyToEdit && selectedCompanyToEdit.id);
    const selectedCompanyId = hasCompany ? selectedCompanyToEdit.id : undefined;
    // Only use the current Designation field for relationship_type
    const selectedRelationship = hasCompany ? (form.designation || '').trim() : '';

    // Always send phone with a leading '+' in payload
    const withPlus = (val) => {
      if (!val) return val;
      return String(val).startsWith('+') ? String(val) : `+${val}`;
    };

    const customerPayload = {
      chat_id: currentChatId,
      first_name: form.firstName,
      last_name: form.lastName,
      email: form.email,
      phone: withPlus(form.phone),
      street_address: form.street,
      city: form.city,
      state: form.state,
      country: form.country,
      source: form.leadSource,
      sync_to_phone: !!syncToPhone,
      ...(hasCompany && selectedCompanyId ? { company_id: selectedCompanyId } : {}),
      ...(hasCompany ? { relationship_type: selectedRelationship } : {})
    };

    // Note: New API expects company_id only; creating companies inline is not supported here.
    
    // Save/update contact in phone address book via WPP
    try {
      await saveContactToPhone(currentChatId, form.firstName, form.lastName, !!syncToPhone);
    } catch (e) {
      // Non-blocking
    }

    if (customerId) {
      // Update existing customer
      if (!customerId || customerId === 'undefined' || customerId === undefined) {
        setIsLoading(false);
        return;
      }
      
      const updatePayload = { customer_id: customerId };
      // Only send non-chat_id fields
      Object.keys(customerPayload).forEach(key => {
        if (key !== 'chat_id') {
          updatePayload[key] = customerPayload[key];
        }
      });
      
      postWindowMessage('CUSTOMER_UPDATE', updatePayload);
    } else {
      // Create new customer
      postWindowMessage('CUSTOMER_CREATE', customerPayload);
    }
  };

  const handleCancel = () => {
    if (isEditMode) {
      // Cancel edit mode and revert to view mode
      setIsEditMode(false);
      // Optionally reset form to initial values
      setForm(initialForm);
      setFirstNameError('');
      setShowCompanySection(false);
    } else {
      closeSidePanel();
    }
  };

  const handleEdit = () => {
    // Snapshot current state so isDirty only reflects new edits
    setInitialForm(prev => ({ ...form }));
    setInitialSyncToPhone(syncToPhone);
    setIsEditMode(true);
  };

  const handleDelete = () => {
    const confirmed = window.confirm('Are you sure you want to delete this customer info?');
    if (!confirmed) return;
    const id = customerId || getCustomerIdFromStorage(currentChatId);
    if (id) postWindowMessage('CUSTOMER_DELETE', { customer_id: id });;
    setIsLoading(true);
    try { removeContactFromPhone(currentChatId); } catch (_) {}
    const unsavedForm = {
      ...form,
      email: '', street: '', city: '', state: '', country: '',
      leadSource: '', relationshipType: '', companyName: '', companyDomain: '', companyIndustry: '', companyQuery: '', designation: ''
    };
    setForm(unsavedForm);
    setInitialForm(unsavedForm);
    setSelectedCompanyToEdit(null);
    setCustomerId(null);
    setShowCompanySection(false);
    setSyncToPhone(false);
    setInitialSyncToPhone(false);
    setIsPhoneBookContact(false);
    setIsLoading(false);
    setIsEditMode(false);
    setSuccessMessage((data && (data.message || data.error)) || 'Deleted successfully.');
    setShowSuccessToast(true);
    setTimeout(() => setShowSuccessToast(false), 2000);
  };

  const handleKeyDownCapture = (e) => {
    // Prevent WhatsApp Web from hijacking Tab navigation
    if (e.key === 'Tab') {
      e.stopPropagation();
      // Allow default so focus moves to the next input naturally
      // Close any open company dropdown when tabbing away
      if (showCompanyDropdown) {
        setShowCompanyDropdown(false);
      }
    }
  };

  return (
    <>
      {currentChatId && !isGroupChat && !shouldShowCard && (
        <div className="cq-contact-alert-header">
          <div className="cq-contact-alert-status">
            <span className="cq-contact-alert-icon">
              {icons.WARNING}
            </span>
            <span className="cq-contact-alert-text">
              This feature will work when the contact permission is enabled on the phone.
            </span>
          </div>
        </div>
      )}
      <div className="cq-customer-info" onKeyDownCapture={handleKeyDownCapture}>
      {currentChatId && !isGroupChat ? (
        shouldShowCard ? (
          <div className="cq-customer-display-card">
            <div className="cq-customer-display-header">
              <div className="cq-display-name">
                {`${form.firstName || ''} ${form.lastName || ''}`.trim() || 'Unknown Contact'}
              </div>
              <div className="cq-header-actions">
                <button className="cq-btn-edit" onClick={handleEdit} title="Edit">
                  {icons.EDIT}
                </button>
                <button className="cq-btn-delete" onClick={handleDelete} title="Delete">
                  {icons.DELETE}
                </button>
              </div>
            </div>

            <div className="cq-customer-display-content">

              {selectedCompanyToEdit && (
                <div className="cq-display-company">
                  {selectedCompanyToEdit.name}
                </div>
              )}

              {form.email && (
                <div className="cq-display-info-row">
                  <span className="cq-display-icon">
                    {icons.EMAIL}
                    <span className="cq-display-icon-tooltip">Email</span>
                  </span>
                  <span className="cq-display-text">{form.email}</span>
                </div>
              )}

              {form.phone && (
                <div className="cq-display-info-row">
                  <span className="cq-display-icon">
                    {icons.PHONE}
                    <span className="cq-display-icon-tooltip">Phone</span>
                  </span>
                  <span className="cq-display-text">{form.phone}</span>
                </div>
              )}

              {form.designation && (
                <div className="cq-display-info-row">
                  <span className="cq-display-icon">
                    {icons.DESIGNATION}
                    <span className="cq-display-icon-tooltip">Designation</span>
                  </span>
                  <span className="cq-display-text">{form.designation}</span>
                </div>
              )}

              {form.leadSource && (
                <div className="cq-display-info-row">
                  <span className="cq-display-icon">
                    {icons.LEAD_SOURCE}
                    <span className="cq-display-icon-tooltip">Lead Source</span>
                  </span>
                  <span className="cq-display-text">{form.leadSource}</span>
                </div>
              )}

              {(form.street || form.city || form.state || form.country) && (
                <div className="cq-display-info-row">
                  <span className="cq-display-icon">
                    {icons.ADDRESS}
                    <span className="cq-display-icon-tooltip">Address</span>
                  </span>
                  <span className="cq-display-text">
                    {[form.street, form.city, form.state, form.country].filter(Boolean).join(', ')}
                  </span>
                </div>
              )}

              {selectedCompanyToEdit?.domain && (
                <div className="cq-display-info-row">
                  <span className="cq-display-icon">
                    {icons.DOMAIN}
                    <span className="cq-display-icon-tooltip">Domain</span>
                  </span>
                  <span className="cq-display-text">{selectedCompanyToEdit.domain}</span>
                </div>
              )}

              {selectedCompanyToEdit?.industry && (
                <div className="cq-display-info-row">
                  <span className="cq-display-icon">
                    {icons.INDUSTRY}
                    <span className="cq-display-icon-tooltip">Industry</span>
                  </span>
                  <span className="cq-display-text">{selectedCompanyToEdit.industry}</span>
                </div>
              )}

              {/* <div className="cq-customer-status">
                <div className="cq-status-item">
                  <div className={`cq-status-check ${customerId ? 'cq-status-active' : 'cq-status-inactive'}`}>
                    {customerId ? icons.CHECK : <div className="cq-empty-circle"></div>}
                  </div>
                  <span className="cq-status-label">WhatsApp</span>
                </div>
                <div className="cq-status-item">
                  <div className={`cq-status-check ${isPhoneBookContact ? 'cq-status-active' : 'cq-status-inactive'}`}>
                    {isPhoneBookContact ? icons.CHECK : <div className="cq-empty-circle"></div>}
                  </div>
                  <span className="cq-status-label">Phone</span>
                </div>
              </div> */}
            </div>
          </div>
        ) : (
      <div className="cq-customer-card">
        <Section first={true}>
          <div className="cq-grid-responsive">
            <Field label={<>First Name<span style={{ color: '#ef4444' }}> *</span></>}>
              <InputBox value={form.firstName} onChange={update('firstName')} />
              {firstNameError && (
                <div className="cq-error-text">{firstNameError}</div>
              )}
            </Field>
            <Field label="Last Name">
              <InputBox value={form.lastName} onChange={update('lastName')} />
            </Field>
          </div>
          <div className="cq-grid-responsive">
            <Field label="Email">
              <InputBox type="email" value={form.email} onChange={update('email')} />
            </Field>
            <Field label={<>Phone Number<span style={{ color: '#ef4444' }}> *</span></>}>
              <InputBox type="tel" value={form.phone} onChange={update('phone')} disabled />
            </Field>
          </div>
          <Field label="Lead Source">
            <InputBox value={form.leadSource} onChange={update('leadSource')} />
          </Field>
          <Field label="Company">
            <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
              <div className="company-search-container" style={{ position: 'relative', flex: 1 }}>
                <InputBox
                  placeholder="Type to search companies..."
                  name="lead_company"
                  autoComplete="off"
                  value={form.companyQuery}
                  onChange={selectedCompanyToEdit ? undefined : handleCompanySearch}
                  onFocus={selectedCompanyToEdit ? undefined : handleCompanyInputFocus}
                  onKeyDown={(e) => { if (e.key === 'Tab') setShowCompanyDropdown(false); }}
                  disabled={!!selectedCompanyToEdit}
                />
                {showCompanyDropdown && (
                  <div className="cq-company-dropdown">
                    {companiesLoading && companiesPage === 1 && (
                      <div className="cq-company-dropdown-loading">
                        Loading companies...
                      </div>
                    )}
                    {companies.length > 0 ? (
                      <>
                        {companies.map((company, index) => (
                          <div 
                            key={`${company.name}-${index}`} 
                            onClick={() => handleCompanySelect(company)} 
                            className="cq-company-dropdown-item"
                          >
                            {company.name}
                          </div>
                        ))}
                        {hasMoreCompanies && (
                          <div 
                            onClick={loadMoreCompanies}
                            className="cq-company-dropdown-item cq-company-dropdown-load-more"
                          >
                            {companiesLoading ? 'Loading...' : 'Load more companies'}
                          </div>
                        )}
                      </>
                    ) : (
                      !companiesLoading && searchQuery && (
                        <div 
                          onClick={() => {
                            // Open company form prefilled with typed name
                            setSelectedCompanyToEdit(null);
                            setShowCompanySection(true);
                            setForm(prev => ({
                              ...prev,
                              companyName: searchQuery,
                              // Keep the search value visible in the inline field
                              companyQuery: searchQuery
                            }));
                            setCompanyError('');
                          }}
                          className="cq-company-dropdown-item cq-company-dropdown-create"
                          style={{
                            backgroundColor: 'var(--color-panel-solid)',
                            color: 'var(--color-accent-solid)',
                            fontWeight: '500',
                            borderTop: '1px solid var(--color-border)'
                          }}
                        >
                          Create company "{searchQuery}"
                        </div>
                      )
                    )}
                    {(!companiesLoading && !searchQuery && companies.length === 0) && (
                      <div className="cq-company-dropdown-loading">
                        No companies available
                      </div>
                    )}
                  </div>
                )}
              </div>
              {selectedCompanyToEdit && (
                <>
                  <button
                    type="button"
                    className="cq-icon-btn"
                    onClick={() => {
                      setShowCompanySection(true);
                      if (selectedCompanyToEdit) {
                        setForm(prev => ({
                          ...prev,
                          companyName: selectedCompanyToEdit.name || '',
                          companyDomain: selectedCompanyToEdit.domain || '',
                          companyIndustry: selectedCompanyToEdit.industry || ''
                        }));
                      }
                      setCompanyError('');
                    }}
                    title="Edit company"
                  >
                    {icons.EDIT}
                  </button>
                  <button
                    type="button"
                    className="cq-icon-btn cq-icon-btn--danger"
                    onClick={() => {
                      setSelectedCompanyToEdit(null);
                      setCompanyError('');
                      setForm(prev => ({
                        ...prev,
                        companyQuery: '',
                        companyName: '',
                        companyDomain: '',
                        companyIndustry: '',
                        relationshipType: '',
                        designation: ''
                      }));
                    }}
                    title="Remove company"
                  >
                    {icons.CROSS_CIRCLED}
                  </button>
                </>
              )}
            </div>
          </Field>
          
          <Field label="Designation">
            <InputBox 
              value={form.designation} 
              onChange={update('designation')} 
            />
          </Field>



          {showCompanySection && (
            <div className="cq-company-card" style={{ marginTop: '12px' }}>
              <div className="cq-company-heading">{selectedCompanyToEdit ? 'Update Company' : 'Create Company'}</div>
              <div className="cq-grid-responsive">
                <Field label={<>Name<span style={{ color: '#ef4444' }}> *</span></>} full>
                  <InputBox value={form.companyName} onChange={update('companyName')} />
                  {companyError && (
                    <div className="cq-error-text" style={{ marginTop: '4px' }}>{companyError}</div>
                  )}
                </Field>
                <Field label="Domain" full>
                  <InputBox value={form.companyDomain} onChange={update('companyDomain')} />
                </Field>
                <Field label="Industry" full>
                  <InputBox value={form.companyIndustry} onChange={update('companyIndustry')} />
                </Field>
              </div>
              
              {/* Add Company Button inside form */}
              <div style={{ marginTop: '12px', display: 'flex', gap: '8px', justifyContent: 'flex-end' }}>
                <button 
                  type="button" 
                  onClick={() => (selectedCompanyToEdit ? updateCompany() : createCompany())}
                  disabled={companySaving || !(form.companyName && form.companyName.trim())}
                  style={{
                    padding: '6px 16px',
                    fontSize: '14px',
                    backgroundColor: companySaving ? '#9ca3af' : ((form.companyName && form.companyName.trim()) ? '#10b981' : '#9ca3af'),
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: companySaving ? 'not-allowed' : ((form.companyName && form.companyName.trim()) ? 'pointer' : 'not-allowed')
                  }}
                >
                  {companySaving ? 'Saving...' : (selectedCompanyToEdit ? 'Update Company' : 'Create Company')}
                </button>
                <button 
                  type="button" 
                  onClick={handleCompanyFormCancel}
                  style={{
                    padding: '6px 16px',
                    fontSize: '14px',
                    backgroundColor: '#6b7280',
                    color: 'white',
                    border: 'none',
                    borderRadius: '4px',
                    cursor: 'pointer'
                  }}
                  onMouseEnter={(e) => e.target.style.backgroundColor = '#4b5563'}
                  onMouseLeave={(e) => e.target.style.backgroundColor = '#6b7280'}
                >
                  Cancel
                </button>
              </div>
            </div>
          )}
        </Section>

        <section className="cq-customer-section">
          <div 
            className="cq-customer-section-title-collapsible" 
            onClick={() => setIsAddressCollapsed(!isAddressCollapsed)}
          >
            <h4 className="cq-customer-section-title" style={{ margin: 0 }}>Address</h4>
            <div className="cq-caret" style={{ transform: isAddressCollapsed ? 'rotate(0deg)' : 'rotate(180deg)' }}>
              {icons.DOWN_ARROW}
            </div>
          </div>
          {!isAddressCollapsed && (
            <>
              <Field label="Street Address">
                <TextAreaBox value={form.street} onChange={update('street')} />
              </Field>
              <div className="cq-grid-responsive-3">
                <Field label="City">
                  <InputBox value={form.city} onChange={update('city')} />
                </Field>
                <Field label="State/Province">
                  <InputBox value={form.state} onChange={update('state')} />
                </Field>
                <Field label="Country" full>
                  <InputBox value={form.country} onChange={update('country')} />
                </Field>
              </div>
            </>
          )}
        </section>

        <div className="cq-actions">
          <div style={{marginRight: 'auto'}}>
            <Checkbox id="sync-to-phone" checked={syncToPhone} onChange={(e)=> setSyncToPhone(e.target.checked)} label="Sync to phone" />
          </div>
          <button className="cq-btn-secondary" type="button" onClick={handleCancel}>Cancel</button>
          <button className="cq-btn-primary" type="button" disabled={isLoading} onClick={handleSave}>
            {isLoading ? 'Saving...' : 'Save'}
          </button>
        </div>
        {showSuccessToast && createPortal(
          <div className="cq-toast">{successMessage || 'Saved successfully.'}</div>,
          document.body
        )}
      </div>
        )
      ) : (
        <div className="cq-customer-container">
          <div className="cq-customer-empty-state">
            <div className="cq-customer-empty-icon">{icons.INFO_CIRCLE}</div>
            <div className="cq-customer-empty-title">
              {isGroupChat ? 'Select a personal chat' : 'Select Chat'}
            </div>
            <div className="cq-customer-empty-description">
              {isGroupChat
                ? "Contact Info doesn't work for groups."
                : 'Please select a chat to see the customer profile.'}
            </div>
          </div>
        </div>
      )}
    </div>
    </>
  );
};

export default CustomerInfo;
