___CSS_LOADER_EXPORT___.push([module.id, `.cq-icon-container {
  position: relative;
  padding: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  color: #333;
}

body.dark .cq-icon-container {
  color: #e9edef;
}

.cq-icon-tooltip {
  position: absolute;
  top: 50%;
  left: -14px;
  transform: translate(-100%, -50%);
  background-color: #000;
  color: #fff;
  font-size: 13px;
  padding: 6px 10px;
  border-radius: 20px;
  white-space: nowrap;
  box-shadow: 0 1px 6px rgba(0,0,0,0.2);
  z-index: 99999;
  margin-right: 8px;
  margin-left: 4px;
}

body.dark .cq-icon-tooltip {
  background-color: #fff;
  color: #000;
}

.cq-icon-badge {
  position: absolute;
  top: -11px;
  right: -11px;
  background-color: red;
  color: white;
  border-radius: 25px;
  font-size: 10px;
  padding: 0 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 22px;
  min-width: 18px;
}

.cq-icon-badge.danger {
  background-color: red;
}

.cq-icon-badge.success {
  background-color: green;
}

.cq-icon-badge.warning {
  background-color: orange;
}

.cq-icon-badge.info {
  background-color: blue;
}

.cq-icon-label {
  font-size: 10px;
  text-align: center;
