.cq-scheduled-messages-alert-header {
  padding: 12px 16px;
  border-bottom: 1px solid #ffc107;
  background-color: #fff3cd;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  min-height: 30px;
}

body.dark .cq-scheduled-messages-alert-header {
  background-color: #3d3014;
  border-bottom-color: #ffc107;
}

.cq-scheduled-messages-alert-status {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
}

.cq-scheduled-messages-alert-icon {
  font-size: 14px;
}

.cq-scheduled-messages-alert-text {
  font-size: 12px;
  color: #666;
}

body.dark .cq-scheduled-messages-alert-text {
  color: #adb5bd;
}
