export function sendMessageToTab(tabId, type, payload = {}) {
  return new Promise((resolve, reject) => {
    try {
      chrome.tabs.sendMessage(tabId, {
        type,
        ...payload
      }, (response) => {
        if (chrome.runtime.lastError) {
          // Handle "Receiving end does not exist" gracefully
          console.warn(`Failed to send message to tab ${tabId}:`, chrome.runtime.lastError.message);
          resolve({ success: false, error: chrome.runtime.lastError.message });
        } else {
          resolve(response || { success: true });
        }
      });
    } catch (error) {
      console.error('Error sending message to tab:', error);
      reject(error);
    }
  });
}

export const postWindowMessage = (type, payload) => {
  try {
    window.postMessage({ type, payload }, '*');
  } catch (error) {
    console.error(' Failed to post window message:', error.message);
    throw new Error(`Failed to post window message: ${error.message}`);
  }
};

export const sendMessage = (type, payload = {}) => {
  return new Promise((resolve, reject) => {
    // Check if chrome.runtime is available
    if (!chrome?.runtime?.sendMessage) {
      console.warn('Chrome runtime not available for sendMessage');
      reject(new Error('Chrome runtime not available'));
      return;
    }

    try {
      chrome.runtime.sendMessage({ type, payload }, (response) => {
        if (chrome.runtime.lastError) {
          const error = chrome.runtime.lastError;
          // Handle "Receiving end does not exist" more gracefully
          if (error.message?.includes('Receiving end does not exist')) {
            console.warn('Background script not available, message discarded:', type);
            resolve({ success: false, error: 'Background script not available' });
          } else {
            console.error('sendMessage error:', error.message);
            reject(error);
          }
        } else {
          resolve(response);
        }
      });
    } catch (error) {
      console.error('Exception in sendMessage:', error);
      reject(error);
    }
  });
};