import React, { useRef, useEffect } from 'react';

const TextAreaBox = ({ value, onChange, placeholder, disabled = false, name }) => {
  const textareaRef = useRef(null);

  useEffect(() => {
    const el = textareaRef.current;
    if (el) {
      el.style.height = 'auto';
      const maxHeight = 90;
      el.style.height = Math.min(el.scrollHeight, maxHeight) + 'px';
    }
  }, [value]);

  useEffect(() => {
    // Inject CSS styles for textarea
    const styleId = 'cq-textarea-styles';
    let styleElement = document.getElementById(styleId);
    
    if (!styleElement) {
      styleElement = document.createElement('style');
      styleElement.id = styleId;
      document.head.appendChild(styleElement);
    }

    styleElement.textContent = `
      .cq-textarea {
        width: 100%;
        padding: 10px;
        border: 1px solid #ccc;
        border-radius: 6px;
        background: #fff;
        color: #111b21;
        fontSize: 14px;
        outline: none;
        margin-bottom: 4px;
        box-sizing: border-box;
        line-height: 1.4;
        resize: none;
        overflow-y: auto;
        overflow-x: hidden;
        white-space: pre-wrap;
        word-wrap: break-word;
        min-height: 65px;
        font-family: inherit;
        transition: background-color 0.2s ease, border-color 0.2s ease, color 0.2s ease;
      }

      .cq-textarea:disabled {
        background: #f5f5f5;
      }

      body.dark .cq-textarea {
        border: 1px solid #313d45;
        background: #111b21;
        color: #e9edef;
      }

      body.dark .cq-textarea:disabled {
        background: #1e2a32;
      }

      .cq-textarea::placeholder {
        color: #667781;
        opacity: 1;
      }

      body.dark .cq-textarea::placeholder {
        color: #8696a0;
        opacity: 1;
      }

      .cq-textarea::-webkit-input-placeholder {
        color: #667781;
        opacity: 1;
      }

      body.dark .cq-textarea::-webkit-input-placeholder {
        color: #8696a0;
        opacity: 1;
      }

      .cq-textarea::-moz-placeholder {
        color: #667781;
        opacity: 1;
      }

      body.dark .cq-textarea::-moz-placeholder {
        color: #8696a0;
        opacity: 1;
      }

      .cq-textarea:-ms-input-placeholder {
        color: #667781;
        opacity: 1;
      }

      body.dark .cq-textarea:-ms-input-placeholder {
        color: #8696a0;
        opacity: 1;
      }
    `;
  }, []);

  return (
    <textarea
      ref={textareaRef}
      name={name}
      value={value}
      disabled={disabled}
      onChange={onChange}
      placeholder={placeholder}
      className="cq-textarea"
      rows={1}
    />
  );
};

export default TextAreaBox;