import React, { useState } from 'react';
import { sendTextMessage, setInputText } from '../../utils/WppFunctions';
import { icons } from '../../Constants/svg';
import Icon from '../UIComponents/Icon';
import { postWindowMessage } from '../../utils/messageSender';
import { TRACK_EVENTS } from '../../Constants/constant';

export const QuickReplyList = ({ replies, onEdit, onDelete, onAdd, onReorder, isSidebarMode = false }) => {
  const isDark = document.body.classList.contains('dark');
  const [draggedIndex, setDraggedIndex] = useState(null);

  const handleDragStart = (index) => {
    setDraggedIndex(index);
  };

  const handleDragOver = (e) => {
    e.preventDefault(); // Allow drop
  };

  const handleQuickReplyClick = async (message, editBeforeSend = false, replyName = '') => {
    if (!message) return;
    try {
      if (editBeforeSend) {
        await setInputText(message);
      } else {
        await sendTextMessage(message);
      }

      // Track quick reply usage
      postWindowMessage('TRACK_EVENT', {
        eventName: TRACK_EVENTS.QUICK_REPLY_USED,
        properties: {
          replyName: replyName,
          editBeforeSend: editBeforeSend
        }
      });
    } catch (err) {
      console.error('Error sending or inserting quick reply:', err);
    }
  };

  const handleDrop = (index) => {
    if (draggedIndex === null || draggedIndex === index) return;
    const updatedReplies = [...replies];
    const [movedItem] = updatedReplies.splice(draggedIndex, 1);
    updatedReplies.splice(index, 0, movedItem);
    onReorder(updatedReplies);
    setDraggedIndex(null);
  };

  return (
    <div
      style={{
        ...(isSidebarMode ? {
          // Sidebar mode styles
          padding: '0',
          border: 'none',
          borderRadius: '0',
          backgroundColor: 'transparent',
          boxShadow: 'none',
          minWidth: 'auto',
        } : {
          // Floating mode styles
          padding: '16px',
          border: isDark ? '1px solid #2a3942' : '1px solid #ccc',
          borderRadius: '12px',
          backgroundColor: isDark ? '#202c33' : '#ffffff',
          boxShadow: '0 4px 12px rgba(0,0,0,0.15)',
          minWidth: '280px',
        })
      }}
    >
      <div style={{ marginBottom: isSidebarMode ? '0' : '12px' }}>
        {replies.length === 0 ? (
          <div style={{
            ...(isSidebarMode ? {
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              justifyContent: 'center',
              padding: '40px 20px',
              textAlign: 'center',
              color: isDark ? '#adb5bd' : '#666',
              height: '200px'
            } : {}),
            fontSize: '14px', 
            color: isDark ? '#ccc' : '#666'
          }}>
            {isSidebarMode ? (
              <>
                <div style={{
                  fontSize: '48px',
                  color: isDark ? '#6b7280' : '#999',
                  marginBottom: '20px',
                  opacity: 0.5
                }}></div>
                <div style={{
                  fontSize: '18px',
                  fontWeight: '600',
                  color: isDark ? '#e9edef' : '#333',
                  marginBottom: '8px'
                }}>No quick replies yet</div>
                <div style={{
                  fontSize: '14px',
                  color: isDark ? '#adb5bd' : '#666',
                  lineHeight: '1.5',
                  maxWidth: '280px'
                }}>
                  Create quick replies to save time sending common messages.
                </div>
              </>
            ) : (
              'No quick replies found.'
            )}
          </div>
        ) : (
          <ul
            style={{
              listStyle: 'none',
              padding: 0,
              margin: 0,
              display: 'flex',
              flexDirection: 'column',
              gap: isSidebarMode ? '10px' : '10px',
            }}
          >
            {replies.map((reply, index) => (
              <li
                key={index}
                className={isSidebarMode ? "quick-reply-item" : ""}
                onDragOver={handleDragOver}
                onDrop={() => handleDrop(index)}
                onClick={() => handleQuickReplyClick(reply.message, reply.editBeforeSend, reply.name)}
                style={{
                  ...(isSidebarMode ? {
                    backgroundColor: isDark ? '#2a3942' : '#f9f9f9',
                    border: `1px solid ${isDark ? '#3b4a54' : '#e0e0e0'}`,
                    padding: '12px',
                    borderRadius: '8px',
                    cursor: 'pointer',
                    transition: 'all 0.2s ease',
                    userSelect: 'none',
                    display: 'flex',
                    alignItems: 'flex-start',
                    gap: '8px'
                  } : {
                    backgroundColor: isDark ? '#2a3942' : '#f9f9f9',
                    padding: '12px 16px',
                    borderRadius: '10px',
                    boxShadow: '0 2px 6px rgba(0,0,0,0.05)',
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    userSelect: 'none',
                    cursor: 'pointer',
                  })
                }}
                onMouseEnter={(e) => {
                  if (isSidebarMode) {
                    e.target.style.backgroundColor = isDark ? '#3b4a54' : '#e8f4f8';
                    e.target.style.borderColor = isDark ? '#00d9ff' : '#007bff';
                    e.target.style.transform = 'translateY(-1px)';
                    e.target.style.boxShadow = isDark ? '0 2px 8px rgba(0, 217, 255, 0.2)' : '0 2px 8px rgba(0, 123, 255, 0.2)';
                  }
                }}
                onMouseLeave={(e) => {
                  if (isSidebarMode) {
                    e.target.style.backgroundColor = isDark ? '#2a3942' : '#f9f9f9';
                    e.target.style.borderColor = isDark ? '#3b4a54' : '#e0e0e0';
                    e.target.style.transform = 'translateY(0)';
                    e.target.style.boxShadow = 'none';
                  }
                }}
              >
                <style>{`
                  .quick-reply-item:hover .quick-reply-actions {
                    opacity: 1 !important;
                  }
                `}</style>
                {isSidebarMode ? (
                  <>
                    <span
                      draggable
                      onDragStart={() => handleDragStart(index)}
                      style={{
                        cursor: 'grab',
                        fontSize: '14px',
                        color: isDark ? '#8696a0' : '#666',
                        padding: '2px',
                        lineHeight: 1,
                        marginTop: '2px'
                      }}
                      title="Drag to reorder"
                      onClick={(e) => e.stopPropagation()}
                    >
                      
                    </span>
                    <div style={{ flex: 1, minWidth: 0 }}>
                      <div style={{
                        fontWeight: 600,
                        fontSize: '14px',
                        color: isDark ? '#e9edef' : '#333',
                        marginBottom: '4px',
                        wordBreak: 'break-word'
                      }}>
                        {reply.name}
                      </div>
                      <div style={{
                        fontSize: '12px',
                        color: isDark ? '#8696a0' : '#666',
                        lineHeight: 1.4,
                        wordBreak: 'break-word'
                      }}>
                        {reply.message.length > 50 
                          ? `${reply.message.substring(0, 50)}...` 
                          : reply.message}
                      </div>
                      {reply.editBeforeSend && (
                        <div style={{
                          display: 'inline-block',
                          backgroundColor: isDark ? '#1a365d' : '#e3f2fd',
                          color: isDark ? '#90cdf4' : '#1976d2',
                          fontSize: '10px',
                          fontWeight: 500,
                          padding: '2px 6px',
                          borderRadius: '4px',
                          marginTop: '4px'
                        }}>
                          Edit before send
                        </div>
                      )}
                    </div>
                    <div style={{ 
                      display: 'flex', 
                      width: '103px', 
                      justifyContent: 'space-between',
                      opacity: 0,
                      transition: 'opacity 0.2s ease'
                    }} className="quick-reply-actions">
                      <Icon 
                        icon={icons.EDIT} 
                        tooltip="Edit Reply" 
                        label="Edit"
                        onClick={(e) => {
                          e.stopPropagation();
                          onEdit(reply, index);
                        }}
                      />
                      <Icon 
                        icon={icons.DELETE} 
                        tooltip="Delete Reply" 
                        label="Delete"
                        onClick={(e) => {
                          e.stopPropagation();
                          if (window.confirm('Are you sure you want to delete this quick reply?')) {
                            onDelete(index);
                          }
                        }}
                      />
                    </div>
                  </>
                ) : (
                  <>
                    <div style={{ display: 'flex', alignItems: 'center', gap: '12px' }}>
                      <span
                        draggable
                        onDragStart={() => handleDragStart(index)}
                        style={{
                          cursor: 'grab',
                          fontSize: '18px',
                          userSelect: 'none',
                        }}
                        title="Drag to reorder"
                        onClick={(e) => e.stopPropagation()}
                      >
                        
                      </span>
                      <span
                        style={{
                          fontSize: '14px',
                          fontWeight: 500,
                          color: isDark ? '#e9edef' : '#333',
                          wordBreak: 'break-word',
                        }}
                      >
                        {reply.name}
                      </span>
                    </div>

                    <div style={{ display: 'flex', gap: '10px' }}>
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          onEdit(reply, index);
                        }}
                        style={{
                          background: 'none',
                          border: 'none',
                          color: '#007bff',
                          cursor: 'pointer',
                          fontSize: '13px',
                          padding: '2px 6px',
                        }}
                      >
                        Edit
                      </button>
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          if (window.confirm('Are you sure you want to delete this quick reply?')) {
                            onDelete(index);
                          }
                        }}
                        style={{
                          background: 'none',
                          border: 'none',
                          color: '#dc3545',
                          cursor: 'pointer',
                          fontSize: '13px',
                          padding: '2px 6px',
                        }}
                      >
                        Delete
                      </button>
                    </div>
                  </>
                )}
              </li>
            ))}
          </ul>
        )}
      </div>

      {!isSidebarMode && (
        <div style={{ textAlign: 'right', paddingTop: '10px' }}>
          <button
            onClick={onAdd}
            title="Add Quick Reply"
            style={{
              backgroundColor: '#007bff',
              color: '#fff',
              border: 'none',
              borderRadius: '8px',
              width: '32px',
              height: '32px',
              fontSize: '18px',
              fontWeight: 'bold',
              cursor: 'pointer',
              transition: 'background-color 0.2s ease',
            }}
            onMouseEnter={(e) => (e.target.style.backgroundColor = '#006ae0')}
            onMouseLeave={(e) => (e.target.style.backgroundColor = '#007bff')}
          >
            +
          </button>
        </div>
      )}
    </div>
  );
};
