import React from 'react';
import ReactDOM from 'react-dom/client';
import SidePanel from '../Components/UIComponents/SidePanel';

export const renderSidePanel = (Component, props = {}, options = {}) => {
  const {
    title = '',
    className = '',
    containerId = 'generic-side-panel-container',
    panelWidth = '30%',
    minPanelWidth = '300px',
    maxPanelWidth = '60%'
  } = options;

  // Get saved width from localStorage or use default
  const getSavedWidth = () => {
    try {
      const savedWidth = localStorage.getItem('cq-sidepanel-width');
      if (savedWidth) {
        // Validate that saved width is within constraints
        const widthValue = parseFloat(savedWidth);
        if (savedWidth.includes('%') && widthValue >= 25 && widthValue <= 60) {
          return savedWidth;
        }
      }
    } catch (e) {
      console.warn('Failed to load saved sidepanel width:', e);
    }
    return panelWidth;
  };

  // Remove any existing panel with the same container ID (ensure React unmount)
  const oldPanel = document.getElementById(containerId);
  if (oldPanel){
    try {
      if (oldPanel.__cqRoot) {
        oldPanel.__cqRoot.unmount();
      }
    } catch (e) {
      console.warn('Failed to unmount previous panel root:', e);
    }
    console.log('oldPanel sidepanel removed.');
    oldPanel.remove();
    return;
  }

  // Create new panel container
  const container = document.createElement('div');
  container.id = containerId;
  container.className = 'cq-wide-sidepanel';
  container.style.width = getSavedWidth();
  container.style.minWidth = minPanelWidth;
  container.style.maxWidth = maxPanelWidth;
  container.style.height = '100vh';
  container.style.transition = 'width 0.2s ease';
  container.style.display = 'flex';
  container.style.zIndex = '400';

  const header = document.querySelector('#app header');
  if (!header) return;

  const target = header.parentElement;
  if (!target || target.querySelector('.cq-wide-sidepanel')){
    const existing = target.querySelector('.cq-wide-sidepanel');
    if (existing) {
      try {
        if (existing.__cqRoot) {
          existing.__cqRoot.unmount();
        }
      } catch (e) {
        console.warn('Failed to unmount existing sidepanel root:', e);
      }
      console.log('cq-wide-sidepanel sidepanel removed.');
      existing.remove();
    }
  } 

  // target.appendChild(container);
  target.insertBefore(container, document.querySelector('.cq-collapsible-sidebar'));

  // Create close handler that cleans up the panel
  const handleClose = () => {
    console.log('handleClose sidepanel called.');
    try {
      if (container.__cqRoot) {
        container.__cqRoot.unmount();
      }
    } catch (e) {
      console.warn('Failed to unmount sidepanel root on close:', e);
    }
    container.remove();
  };

  // Render the component inside the SidePanel
  const root = ReactDOM.createRoot(container);
  root.render(
    <SidePanel 
      title={title} 
      onClose={handleClose} 
      className={className}
    >
      <Component {...props} />
    </SidePanel>
  );
  // Attach root to container for proper cleanup later
  container.__cqRoot = root;

  return {
    container,
    close: handleClose
  };
};

export const closeSidePanel = () => {
  console.log('close sidepanel called.');
  const panel = document.querySelector('.cq-wide-sidepanel');
  if (panel) {
    try {
      if (panel.__cqRoot) {
        panel.__cqRoot.unmount();
      }
    } catch (e) {
      console.warn('Failed to unmount sidepanel root on closeSidePanel:', e);
    }
    panel.remove();
  }
};
