/**
 * Base Repository - Read-Only IndexedDB Access
 * Provides simplified common DB operations with error handling
 */
export class BaseRepository {
  constructor(dbName, storeName) {
    this.dbName = dbName;
    this.storeName = storeName;
  }

  /**
   * Opens a read-only connection to the IndexedDB
   * @returns {Promise<IDBDatabase>}
   */
  async openDatabase() {
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName);
      request.onsuccess = () => resolve(request.result);
      request.onerror = () => {
        const errorMessage = request.error?.message || 'Unknown error';
        reject(new Error(`Failed to open database '${this.dbName}': ${errorMessage}`));
      };
    });
  }

  /**
   * Ensures the object store exists in the DB
   * @param {IDBDatabase} db
   */
  validateStore(db) {
    if (!db.objectStoreNames.contains(this.storeName)) {
      throw new Error(`Store '${this.storeName}' not found in database '${this.dbName}'`);
    }
  }

  /**
   * Executes a readonly operation within a transaction
   * @param {Function} operation (store: IDBObjectStore) => IDBRequest
   * @returns {Promise<any>}
   */
  async executeRead(operation) {
    const db = await this.openDatabase();
    try {
      this.validateStore(db);
      return await new Promise((resolve, reject) => {
        const tx = db.transaction(this.storeName, 'readonly');
        const store = tx.objectStore(this.storeName);
        const request = operation(store);

        request.onsuccess = () => resolve(request.result);
        request.onerror = () => {
          const err = request.error?.message || 'Read operation failed';
          reject(new Error(`${err} (DB: ${this.dbName})`));
        };

        tx.onerror = () => {
          const err = tx.error?.message || 'Transaction failed';
          reject(new Error(`${err} (DB: ${this.dbName})`));
        };
      });
    } finally {
      db.close();
    }
  }

  /**
   * Gets a record by key
   * @param {string} key
   * @returns {Promise<any>}
   */
  async getByKey(key) {
    this.validateKey(key);
    return this.executeRead(store => store.get(key));
  }

  /**
   * Gets all records from the store
   * @returns {Promise<Array>}
   */
  async getAll() {
    return this.executeRead(store => store.getAll());
  }

  /**
   * Validates the provided key
   * @param {string} key
   */
  validateKey(key) {
    if (!key || typeof key !== 'string') {
      throw new Error('Key must be a non-empty string');
    }
  }

  /**
   * Wraps operation in try/catch with error context
   * @param {Function} operation
   * @param {string} operationName
   * @returns {Promise<any>}
   */
  async safeExecute(operation, operationName) {
    try {
      return await operation();
    } catch (err) {
      const message = err?.message || 'Unknown error';
      throw new Error(`Failed to ${operationName}: ${message}`);
    }
  }
}
