___CSS_LOADER_EXPORT___.push([module.id, `/* Confirmation Modal Styles */
.cq-confirmation-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999999;
  backdrop-filter: blur(2px);
  margin: 0;
  padding: 0;
}

.cq-confirmation-modal {
  background: #ffffff !important;
  border-radius: 24px;
  max-width: 480px;
  width: 90%;
  max-height: 90vh;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  border: none;
  overflow: hidden;
  animation: confirmationModalSlideIn 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

@keyframes confirmationModalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.cq-confirmation-modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 32px 32px 0;
  border-bottom: none;
  background: transparent;
}

.cq-confirmation-modal-title {
  margin: 0;
  font-size: 24px;
  font-weight: 700;
  color: #1a1a1a !important;
  line-height: 1.2;
  letter-spacing: -0.02em;
}

.cq-confirmation-modal-close {
  background: rgba(0, 0, 0, 0.05);
  border: none;
  cursor: pointer;
  padding: 6px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #9ca3af;
  transition: all 0.15s ease;
}

.cq-confirmation-modal-close:hover {
  background: rgba(0, 0, 0, 0.1);
  color: #6b7280;
  transform: scale(1.1);
}

.cq-confirmation-modal-close svg {
  width: 16px;
  height: 16px;
}

.cq-confirmation-modal-body {
  padding: 24px 32px 32px;
}

.cq-confirmation-modal-message {
  margin: 0;
  color: #6b7280 !important;
  line-height: 1.6;
  font-size: 15px;
  white-space: pre-line;
  font-weight: 400;
}

.cq-confirmation-modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  padding: 0 32px 32px;
  border-top: none;
  background: transparent;
}

.cq-confirmation-cancel,
.cq-confirmation-confirm-primary,
.cq-confirmation-confirm-danger {
  padding: 12px 24px;
  border-radius: 12px;
  font-size: 15px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.15s ease;
  border: none;
  min-width: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 0;
}

.cq-confirmation-cancel {
  background: #f3f4f6;
  color: #6b7280;
}

.cq-confirmation-cancel:hover {
  background: #e5e7eb;
  color: #4b5563;
}

.cq-confirmation-confirm-primary {
  background: #10b981;
  color: white;
}

.cq-confirmation-confirm-primary:hover {
  background: #059669;
}

.cq-confirmation-confirm-danger {
  background: #dc2626;
  color: white;
}

.cq-confirmation-confirm-danger:hover {
  background: #b91c1c;
}

/* Dark mode support using body.dark class like SidePanel */
body.dark .cq-confirmation-modal {
  background: #111b21 !important;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.4), 0 10px 10px -5px rgba(0, 0, 0, 0.2);
}

body.dark .cq-confirmation-modal-title {
  color: #e9edef !important;
}

body.dark .cq-confirmation-modal-message {
  color: #adb5bd !important;
}

body.dark .cq-confirmation-modal-close {
  background: rgba(255, 255, 255, 0.1);
  color: #adb5bd;
}

body.dark .cq-confirmation-modal-close:hover {
  background: #374151;
  color: #e9edef;
}

body.dark .cq-confirmation-cancel {
  background: #374151;
  color: #d1d5db;
}

body.dark .cq-confirmation-cancel:hover {
  background: #4b5563;
  color: #f3f4f6;
}

body.dark .cq-confirmation-confirm-primary {
  background: #10b981;
  color: white;
}

body.dark .cq-confirmation-confirm-primary:hover {
  background: #059669;
}

body.dark .cq-confirmation-confirm-danger {
  background: #dc2626;
  color: white;
}

body.dark .cq-confirmation-confirm-danger:hover {
  background: #b91c1c;
}`, "",{"version":3,"sources":["webpack://./src/Components/UIComponents/ConfirmationModal.css"],"names":[],"mappings":"AAAA,8BAA8B;AAC9B;EACE,eAAe;EACf,MAAM;EACN,OAAO;EACP,YAAY;EACZ,aAAa;EACb,8BAA8B;EAC9B,aAAa;EACb,uBAAuB;EACvB,mBAAmB;EACnB,eAAe;EACf,0BAA0B;EAC1B,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,8BAA8B;EAC9B,mBAAmB;EACnB,gBAAgB;EAChB,UAAU;EACV,gBAAgB;EAChB,qFAAqF;EACrF,YAAY;EACZ,gBAAgB;EAChB,0EAA0E;AAC5E;;AAEA;EACE;IACE,UAAU;IACV,wCAAwC;EAC1C;EACA;IACE,UAAU;IACV,iCAAiC;EACnC;AACF;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,oBAAoB;EACpB,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,SAAS;EACT,eAAe;EACf,gBAAgB;EAChB,yBAAyB;EACzB,gBAAgB;EAChB,uBAAuB;AACzB;;AAEA;EACE,+BAA+B;EAC/B,YAAY;EACZ,eAAe;EACf,YAAY;EACZ,kBAAkB;EAClB,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,cAAc;EACd,0BAA0B;AAC5B;;AAEA;EACE,8BAA8B;EAC9B,cAAc;EACd,qBAAqB;AACvB;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,SAAS;EACT,yBAAyB;EACzB,gBAAgB;EAChB,eAAe;EACf,qBAAqB;EACrB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,yBAAyB;EACzB,SAAS;EACT,oBAAoB;EACpB,gBAAgB;EAChB,uBAAuB;AACzB;;AAEA;;;EAGE,kBAAkB;EAClB,mBAAmB;EACnB,eAAe;EACf,gBAAgB;EAChB,eAAe;EACf,0BAA0B;EAC1B,YAAY;EACZ,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iBAAiB;AACnB;;AAEA;EACE,mBAAmB;EACnB,cAAc;AAChB;;AAEA;EACE,mBAAmB;EACnB,cAAc;AAChB;;AAEA;EACE,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,mBAAmB;AACrB;;AAEA,2DAA2D;AAC3D;EACE,8BAA8B;EAC9B,oFAAoF;AACtF;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,oCAAoC;EACpC,cAAc;AAChB;;AAEA;EACE,mBAAmB;EACnB,cAAc;AAChB;;AAEA;EACE,mBAAmB;EACnB,cAAc;AAChB;;AAEA;EACE,mBAAmB;EACnB,cAAc;AAChB;;AAEA;EACE,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,mBAAmB;EACnB,YAAY;AACd;;AAEA;EACE,mBAAmB;AACrB","sourcesContent":["/* Confirmation Modal Styles */\n.cq-confirmation-modal-overlay {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100vw;\n  height: 100vh;\n  background: rgba(0, 0, 0, 0.6);\n  display: flex;\n  justify-content: center;\n  align-items: