import React, { useState } from 'react';
import './Icon.css';

const Icon = ({ icon, tooltip, onClick, badge, badgeType, label='' }) => {
  const [hover, setHover] = useState(false);


  return (
    <div
      className="cq-icon-container"
      onMouseEnter={() => setHover(true)}
      onMouseLeave={() => setHover(false)}
      onClick={onClick}
    >
      <div style={{ position: 'relative' }}>
        {icon}
        {badge > 0 && <div className={`cq-icon-badge ${badgeType || 'danger'}`}>{badge}</div>}
      </div>
      {label && <small className="cq-icon-label">{label}</small>}
      {hover && <div className="cq-icon-tooltip">{tooltip}</div>}
    </div>
  );
};

export default Icon;